import {GameStateType} from '../game-type';
import {PointType} from '../point/point-type';
import {GamePlayerTeamIdEnum} from '../player/player-type';
import {getTeamIdByPlayerId} from '../player/player-helper';
import {GameBuildingStateEnum, GameBuildingType} from '../building/building-type';
import {getBuildingByPoint} from '../building/building-helper';

import {getUnitInfoByUnit} from './unit-helper';
import {GameUnitType} from './unit-type';
import {getUnitAllAvailableAttackPointList} from './unit-to-unit-attack';

// eslint-disable-next-line id-length
export function getUnitAvailableUnitToBuildingAttack(unit: GameUnitType, gameState: GameStateType): Array<PointType> {
    // destroy only enemy building, no noman's building
    const {destroyBuildingList} = getUnitInfoByUnit(unit);

    if (destroyBuildingList.length === 0) {
        return [];
    }

    const {playerList, buildingList} = gameState;
    const playerTeamId: GamePlayerTeamIdEnum = getTeamIdByPlayerId(unit.gamePlayerId, playerList);

    return getUnitAllAvailableAttackPointList(unit, gameState).filter<PointType>(
        (point: PointType): point is PointType => {
            const building: GameBuildingType | null = getBuildingByPoint(point, buildingList);

            if (!building) {
                return false;
            }

            if (building.state === GameBuildingStateEnum.buildingBroken) {
                return false;
            }

            if (!destroyBuildingList.includes(building.buildingType)) {
                return false;
            }

            return getTeamIdByPlayerId(building.gamePlayerId, playerList) !== playerTeamId;
        }
    );
}
