import {createContext, ReactNode, useCallback, useMemo, useState, useContext} from 'react';

import {GamePopup} from '../../layout/game-popup/game-popup';
import gamePopupStyle from '../../layout/game-popup/game-popup.scss';
import {Locale} from '../locale/locale-context';
import {UiButton} from '../../layout/ui-button/ui-button';

import {PromptArgumentType, PromptContextType, PromptProviderDataItemType} from './prompt-type';
import {defaultPromptContextData} from './prompt-const';
import {createDefaultPromptProviderData} from './prompt-helper';

export const PromptContext = createContext<PromptContextType>(defaultPromptContextData);

export function usePromptContext(): PromptContextType {
    return useContext<PromptContextType>(PromptContext);
}

const {Provider: PromptContextProvider} = PromptContext;

type PromptContextPropsType = {
    children: ReactNode;
};

export function PromptProvider(props: PromptContextPropsType): JSX.Element {
    const [promptDataList, setPromptDataList] = useState<Array<PromptProviderDataItemType>>([
        createDefaultPromptProviderData(0),
        createDefaultPromptProviderData(1),
        createDefaultPromptProviderData(2),
    ]);

    const {children} = props;

    const closePopupByIndex = useCallback((index: number) => {
        setPromptDataList(
            (currentPromptDataList: Array<PromptProviderDataItemType>): Array<PromptProviderDataItemType> => {
                const newPromptDataList: Array<PromptProviderDataItemType> = [...currentPromptDataList];

                newPromptDataList[index] = {
                    ...newPromptDataList[index],
                    isOpen: false,
                };

                return newPromptDataList;
            }
        );
    }, []);

    const providedData: PromptContextType = useMemo(() => {
        return {
            prompt: (data: PromptArgumentType) => {
                setPromptDataList(
                    (currentPromptDataList: Array<PromptProviderDataItemType>): Array<PromptProviderDataItemType> => {
                        const newPromptDataList: Array<PromptProviderDataItemType> = [...currentPromptDataList];

                        newPromptDataList[data.index] = {
                            ...data,
                            isOpen: true,
                        };

                        return newPromptDataList;
                    }
                );
            },
        };
    }, []);

    return (
        <PromptContextProvider value={providedData}>
            {children}
            {promptDataList.map((promptData: PromptProviderDataItemType, index: number): JSX.Element => {
                return (
                    <GamePopup isOpen={promptData.isOpen} key={String(index).trim()}>
                        {promptData.header ? (
                            <h3 className={gamePopupStyle.game_popup__header}>
                                <Locale stringKey={promptData.header} />
                            </h3>
                        ) : null}
                        {promptData.text ? (
                            <p className={gamePopupStyle.game_popup__text}>
                                <Locale stringKey={promptData.text} />
                            </p>
                        ) : null}
                        <div className={gamePopupStyle.game_popup__footer}>
                            <UiButton
                                langKey="UI__YES"
                                onClick={() => {
                                    promptData.onYes();
                                    closePopupByIndex(index);
                                }}
                            />
                            <UiButton
                                langKey="UI__NO"
                                onClick={() => {
                                    promptData.onNo();
                                    closePopupByIndex(index);
                                }}
                            />
                        </div>
                    </GamePopup>
                );
            })}
        </PromptContextProvider>
    );
}
