import {UiButton} from '../../../../layout/ui-button/ui-button';
import {clientGameImage} from '../image/client-game-image';
import {GameActionTypeEnum} from '../../../../provider/game/game-context-type';
import {useGameContext} from '../../../../provider/game/game-context';

import clientGameEndTurnButtonStyle from './client-game-end-turn-button.scss';

export function ClientGameEndTurnButton(): JSX.Element {
    const {pushAction, isEnableEndTurnButton} = useGameContext();

    return (
        <UiButton
            className={clientGameEndTurnButtonStyle.client_game_end_turn_button}
            iconSrc={clientGameImage.endTurn.src}
            isDisabled={!isEnableEndTurnButton}
            isSquared
            langKey="GAME__END_TURN"
            onClick={async () => {
                await pushAction({
                    actionType: GameActionTypeEnum.changeTurn,
                    addUnitList: [],
                    buyUnitType: null,
                    movePointList: [],
                    toPoint: {x: 0, y: 0},
                    unitCurrentPoint: {x: 0, y: 0},
                    unitId: '',
                });
            }}
        />
    );
}
