import {ActionArgumentType, GameAnimationType} from '../game-context-type';
import {GameStateType} from '../../../model/game/game-type';
import {getPlayerByPlayerId} from '../../../model/game/player/player-helper';
import {getUnitByUnitId, getUnitInfoByType} from '../../../model/game/unit/unit-helper';
import {GamePlayerType} from '../../../model/game/player/player-type';
import {GameUnitType} from '../../../model/game/unit/unit-type';
import {defaultUnitData} from '../../../model/game/unit/unit-const';
import {getDeadUnitCost} from '../../../page/client/client-game/unit-store/unit-store-helper';
import {waitForNextFrame} from '../../../util/time';
import {PromiseResolveType} from '../../../util/promise';

export function openStoreAction(data: ActionArgumentType): Promise<void> {
    const {setIsOpenStore} = data;

    setIsOpenStore(true);

    return waitForNextFrame();
}

export function closeStoreAction(data: ActionArgumentType): Promise<void> {
    const {setIsOpenStore} = data;

    setIsOpenStore(false);

    return waitForNextFrame();
}

export function addUnitListAction(data: ActionArgumentType): Promise<void> {
    const {action} = data;
    const {addUnitList} = action;

    console.log('addUnitList', addUnitList);

    return waitForNextFrame();
}

// eslint-disable-next-line max-statements
export async function buyUnitListAction(data: ActionArgumentType): Promise<void> {
    const {action, gameState, setGameState, setAnimationList, removeAnimationByAction} = data;
    const {buyUnitType, toPoint, unitId} = action;
    const {activeGamePlayerId, playerList, unitList, cemetery} = gameState;
    const player = getPlayerByPlayerId(activeGamePlayerId, playerList);
    const newCemetery: Array<GameUnitType> = [...cemetery];

    if (!player) {
        console.error(`can not find player by id: ${activeGamePlayerId}`);
        return waitForNextFrame();
    }

    if (!buyUnitType) {
        console.error('buyUnitType is null');
        return waitForNextFrame();
    }

    await closeStoreAction(data);

    return new Promise<void>((resolve: PromiseResolveType<void>) => {
        setAnimationList((animList: Array<GameAnimationType>) => {
            return [
                ...animList,
                {
                    action,
                    addHealthList: [],
                    animationOnFinish: async (): Promise<void> => {
                        const unitInfo = getUnitInfoByType(buyUnitType);
                        const deadUnit: GameUnitType | null = getUnitByUnitId(unitId, newCemetery);
                        const endUnitCost = deadUnit
                            ? getDeadUnitCost(deadUnit.unitType, deadUnit.deathCount)
                            : unitInfo.cost;
                        const newPlayerList: Array<GamePlayerType> = [...playerList];

                        newPlayerList[newPlayerList.indexOf(player)] = {
                            ...player,
                            money: player.money - endUnitCost,
                        };

                        const newUnit: GameUnitType = {
                            ...defaultUnitData,
                            ...toPoint,
                            gamePlayerId: player.gamePlayerId,
                            unitId,
                            unitType: buyUnitType,
                        };

                        if (deadUnit) {
                            newCemetery.splice(newCemetery.indexOf(deadUnit), 1);
                            newUnit.damageGiven = deadUnit.damageGiven;
                            newUnit.damageReceived = deadUnit.damageReceived;
                            newUnit.deathCount = deadUnit.deathCount;
                        }

                        const newUnitList: Array<GameUnitType> = [...unitList, newUnit];

                        setGameState((currentGameState: GameStateType): GameStateType => {
                            return {
                                ...currentGameState,
                                cemetery: newCemetery,
                                // isOpenStore: false,
                                playerList: newPlayerList,
                                unitList: newUnitList,
                            };
                        });

                        removeAnimationByAction(action);
                        await waitForNextFrame();
                        resolve();
                    },
                },
            ];
        });
    });
}
