// eslint-disable-next-line complexity
export function getMaxSquareByMoveSize(point: PointType, mapSize: SizeType, moveSize: number): Array<PointType> {
    const result: Array<PointType> = [];
    const {x: startX, y: startY} = point;
    const {height, width} = mapSize;

    // eslint-disable-next-line no-loops/no-loops
    for (let x = -moveSize; x <= moveSize; x += 1) {
        // eslint-disable-next-line no-loops/no-loops
        for (let y = -moveSize; y <= moveSize; y += 1) {
            const pointX: number = startX + x;
            const pointY: number = startY + y;

            if (Math.abs(x) + Math.abs(y) > moveSize) {
                // eslint-disable-next-line no-continue
                continue;
            }

            if (pointX === startX && pointY === startY) {
                // eslint-disable-next-line no-continue
                continue;
            }

            if (pointX < 0 || pointY < 0 || pointX >= width || pointY >= height) {
                // eslint-disable-next-line no-continue
                continue;
            }

            result.push({x: pointX, y: pointY});
        }
    }

    return result;
}
