/* global HTMLInputElement */
import {useState, SyntheticEvent, useCallback} from 'react';

import {GamePopup} from '../../../../layout/game-popup/game-popup';
import gamePopupStyle from '../../../../layout/game-popup/game-popup.scss';
import {Locale} from '../../../../provider/locale/locale-context';
import {UiButton} from '../../../../layout/ui-button/ui-button';
import serviceStyle from '../../../../css/service.scss';
import {useGameContext} from '../../../../provider/game/game-context';
import {saveGame} from '../../../../provider/game/action/save-game';
import {GameSnackbar} from '../../../../layout/game-snackbar/game-snackbar';

import clientSaveGamePopupStyle from './client-save-game-popup.scss';

type ClientSaveGamePopupType = {
    handleClose: () => void;
    isSaveGamePopupOpen: boolean;
};

export function ClientSaveGamePopup(props: ClientSaveGamePopupType) {
    const {isSaveGamePopupOpen, handleClose} = props;
    const {gameState} = useGameContext();
    const {stepCount} = gameState;
    const saveDefaultName = `save: ${new Date().toLocaleString()}, step: ${stepCount.toString(10).padStart(4, '0')}`;
    const [saveName, setSaveName] = useState<string>(saveDefaultName);
    const [isGameSavedSnackbarOpen, setIsGameSavedSnackbarOpen] = useState<boolean>(false);

    const handleCloseGameSavedSnackbar = useCallback(() => {
        setIsGameSavedSnackbarOpen(false);
    }, []);

    return (
        <>
            <GamePopup isOpen={isSaveGamePopupOpen}>
                <h3 className={gamePopupStyle.game_popup__header}>
                    <Locale stringKey="SAVE_LOAD__SAVE_GAME" />
                </h3>
                <div className={clientSaveGamePopupStyle.client_save_game_popup__text_input_wrapper}>
                    <input
                        className={serviceStyle.input_text}
                        defaultValue={saveDefaultName}
                        onInput={(evt: SyntheticEvent<HTMLInputElement>) => {
                            setSaveName(evt.currentTarget.value.trim());
                        }}
                        type="text"
                    />
                </div>
                <div className={gamePopupStyle.game_popup__footer}>
                    <UiButton
                        langKey="SAVE_LOAD__SAVE"
                        onClick={() => {
                            saveGame(saveName, gameState);
                            handleClose();
                            setIsGameSavedSnackbarOpen(true);
                        }}
                    />
                    <UiButton langKey="UI__CLOSE" onClick={handleClose} />
                </div>
            </GamePopup>
            <GameSnackbar handleClose={handleCloseGameSavedSnackbar} isOpen={isGameSavedSnackbarOpen}>
                <Locale stringKey="SAVE_LOAD__SAVE_SUCCESSFUL" />
            </GameSnackbar>
        </>
    );
}
