import {useState} from 'react';
import {useNavigate} from 'react-router-dom';

import {UiButton} from '../../../../../layout/ui-button/ui-button';
import {usePromptContext} from '../../../../../provider/prompt/prompt-context';
import {noop} from '../../../../../util/function';
import {ClientSaveGamePopup} from '../../client-save-game-popup/client-save-game-popup';
import {ClientLoadGamePopup} from '../../client-load-game-popup/client-load-game-popup';

import clientGameMenuStyle from './client-game-menu.scss';

type ClientGameMenuType = {
    showGameStarMessage: () => void;
};

export function ClientGameMenu(props: ClientGameMenuType): JSX.Element {
    const {showGameStarMessage} = props;
    const {prompt} = usePromptContext();
    const [isSaveGamePopupOpen, setIsSaveGamePopupOpen] = useState<boolean>(false);
    const [isLoadGamePopupOpen, setIsLoadGamePopupOpen] = useState<boolean>(false);
    const [isQuickLoadGamePopupOpen, setIsQuickLoadGamePopupOpen] = useState<boolean>(false);
    const navigate = useNavigate();

    return (
        <>
            <div className={clientGameMenuStyle.client_game_menu}>
                <div className={clientGameMenuStyle.client_game_menu__button_container}>
                    <UiButton isTextToCenter langKey="NOTIFICATION__OBJECTIVE" onClick={showGameStarMessage} />
                    <UiButton
                        isTextToCenter
                        langKey="SAVE_LOAD__SAVE_GAME"
                        onClick={() => setIsSaveGamePopupOpen(true)}
                    />
                    <UiButton
                        isTextToCenter
                        langKey="SAVE_LOAD__LOAD_GAME"
                        onClick={() => setIsLoadGamePopupOpen(true)}
                    />
                    <UiButton
                        isTextToCenter
                        langKey="SAVE_LOAD__QUICK_LOAD_GAME"
                        onClick={() => setIsQuickLoadGamePopupOpen(true)}
                    />
                    <UiButton
                        isTextToCenter
                        langKey="END_GAME__QUIT_BATTLE"
                        onClick={() => {
                            prompt({
                                header: 'POPUP__DO_YOU_REALLY_WANT_TO_QUIT',
                                index: 0,
                                onNo: noop,
                                onYes: () => navigate(-1),
                                // text: 'SAVE_LOAD__DELETE',
                            });
                        }}
                    />
                </div>
            </div>
            <ClientLoadGamePopup
                handleClose={() => setIsLoadGamePopupOpen(false)}
                isLoadGamePopupOpen={isLoadGamePopupOpen}
                isQuickLoad={false}
            />
            <ClientSaveGamePopup
                handleClose={() => setIsSaveGamePopupOpen(false)}
                isSaveGamePopupOpen={isSaveGamePopupOpen}
            />
            <ClientLoadGamePopup
                handleClose={() => setIsQuickLoadGamePopupOpen(false)}
                isLoadGamePopupOpen={isQuickLoadGamePopupOpen}
                isQuickLoad
            />
        </>
    );
}
