function getOccupyBuildingMovedAction(
    sortedMovedActionMap: Record<GameActionTypeEnum, Array<MovedGameActionType>>
): MovedGameActionType | null {
    const occupyBuildingList = sortedMovedActionMap[occupyBuilding];

    return getMaxInList<MovedGameActionType>(occupyBuildingList, (movedAction: MovedGameActionType): number => {
        // get the far first
        return getLengthBetweenPoints(movedAction.moveFrom, movedAction.moveTo);
    });
}

function getUnitToUnitAttackMovedAction(
    sortedMovedActionMap: Record<GameActionTypeEnum, Array<MovedGameActionType>>,
    gameState: GameStateType
): MovedGameActionType | null {
    const unitToUnitAttackList = sortedMovedActionMap[unitToUnitAttack].filter(
        (movedAction: MovedGameActionType): movedAction is MovedGameActionType => {
            const attackRate: MovedActionAttackRateType = getMovedActionAttackResult(movedAction, gameState);
            const advantage: number = attackRate.additionalDamageGiven - attackRate.additionalDamageReceived;

            return advantage >= 0;
        }
    );

    return getMaxInList<MovedGameActionType>(unitToUnitAttackList, (movedAction: MovedGameActionType): number => {
        const movedActionAttackRate: MovedActionAttackRateType = getMovedActionAttackResult(movedAction, gameState);
        const placeData: MovedActionPlaceDataType = getPlaceData(movedAction, gameState);
        const onBuildingRate: number = getOnBuildingRate(movedAction, placeData.onBuildingState, gameState);

        // get max damage and max defend
        return (
            movedActionAttackRate.additionalDamageGiven -
            movedActionAttackRate.additionalDamageReceived +
            placeData.placeArmor +
            onBuildingRate
        );
    });
}

function getUnitToBuildingAttackMovedAction(
    sortedMovedActionMap: Record<GameActionTypeEnum, Array<MovedGameActionType>>,
    gameState: GameStateType
): MovedGameActionType | null {
    const unitToBuildingAttackList = sortedMovedActionMap[unitToBuildingAttack]
        // do not destroy no man's building
        .filter((movedAction: MovedGameActionType): movedAction is MovedGameActionType => {
            const building = getBuildingByPoint(movedAction.gameAction.toPoint, gameState.buildingList);

            if (!building) {
                return false;
            }

            return building.gamePlayerId !== GamePlayerIdEnum.playerEmpty;
        });

    return getMaxInList<MovedGameActionType>(unitToBuildingAttackList, (movedAction: MovedGameActionType): number => {
        // get far building
        return getLengthBetweenPoints(movedAction.moveFrom, movedAction.gameAction.toPoint);
    });
}

function getUnitRaiseSkeletonMovedAction(
    sortedMovedActionMap: Record<GameActionTypeEnum, Array<MovedGameActionType>>,
    gameState: GameStateType
): MovedGameActionType | null {
    const unitRaiseSkeletonList = sortedMovedActionMap[raiseSkeleton];

    return getMaxInList<MovedGameActionType>(unitRaiseSkeletonList, (movedAction: MovedGameActionType): number => {
        // get most defended adn close place
        const placeDataA: MovedActionPlaceDataType = getPlaceData(movedAction, gameState);
        const onBuildingRateA: number = getOnBuildingRate(movedAction, placeDataA.onBuildingState, gameState);
        // use it cause defend more important than path size
        const pathQualification = 10;

        return (
            onBuildingRateA +
            placeDataA.placeArmor -
            getLengthBetweenPoints(movedAction.moveFrom, movedAction.moveTo) / pathQualification
        );
    });
}

function getUnitFixBuildingMovedAction(
    sortedMovedActionMap: Record<GameActionTypeEnum, Array<MovedGameActionType>>
): MovedGameActionType | null {
    const unitFixBuildingList = sortedMovedActionMap[fixBuilding];

    return getMinInList<MovedGameActionType>(unitFixBuildingList, (movedAction: MovedGameActionType): number => {
        return getLengthBetweenPoints(movedAction.moveFrom, movedAction.moveTo);
    });
}

function getUnitMoveMovedAction(
    sortedMovedActionMap: Record<GameActionTypeEnum, Array<MovedGameActionType>>,
    gameState: GameStateType
): MovedGameActionType | null {
    const unitMoveList: Array<MovedGameActionType> = sortedMovedActionMap[unitMove];
    const buildingToOccupyList: Array<GameBuildingType> = getBuildingListToOccupy(gameState);
    const isExistBuildingToOccupy: boolean = buildingToOccupyList.length > 0;
    const enemyUnitList: Array<GameUnitType> = getEnemyUnitListByPlayerId(
        gameState.activeGamePlayerId,
        gameState.playerList,
        gameState.unitList
    );

    // eslint-disable-next-line complexity, max-statements
    return getMaxInList<MovedGameActionType>(unitMoveList, (movedAction: MovedGameActionType): number => {
        const unitToRate = getUnitByUnitId(movedAction.gameAction.unitId, gameState.unitList);
        const healthActionAdditionalRating = 1e9;
        const minHealthToFight = 65;
        const occupyBuildingAdditionalRating = 1e6;
        const enemyUnitAdditionalRating = 1e3;

        if (!unitToRate) {
            console.error(`can not get unit by id: ${movedAction.gameAction.unitId}`);
            return 0;
        }

        const unitInfo = getUnitInfoByUnit(unitToRate);

        // up health
        if (unitToRate.health < minHealthToFight) {
            const closeBuilding = getCloseBuildingListToHealth(movedAction.moveTo, gameState);

            if (closeBuilding) {
                // remove unitToRate.health, less health move priority
                return (
                    healthActionAdditionalRating -
                    getLengthBetweenPoints(movedAction.moveTo, {x: closeBuilding.x, y: closeBuilding.y}) -
                    unitToRate.health
                );
            }
        }

        const placeData: MovedActionPlaceDataType = getPlaceData(movedAction, gameState);
        const onBuildingStateRate = getOnBuildingRate(movedAction, placeData.onBuildingState, gameState);

        // occupy building
        if (isExistBuildingToOccupy) {
            const closeBuilding = getCloseBuildingListToOccupy(movedAction.moveFrom, buildingToOccupyList);

            if (closeBuilding && unitInfo.occupyBuildingList.includes(closeBuilding.buildingType)) {
                return (
                    occupyBuildingAdditionalRating -
                    getLengthBetweenPoints(movedAction.moveTo, {x: closeBuilding.x, y: closeBuilding.y}) +
                    onBuildingStateRate +
                    placeData.placeArmor
                );
            }
        }

        // fight versus enemy
        if (enemyUnitList.length > 0) {
            const closeEnemyUnit = getCloseEnemyUnit(movedAction.moveTo, enemyUnitList);

            if (closeEnemyUnit) {
                return (
                    enemyUnitAdditionalRating -
                    getLengthBetweenPoints(movedAction.moveTo, {x: closeEnemyUnit.x, y: closeEnemyUnit.y}) +
                    onBuildingStateRate +
                    placeData.placeArmor
                );
            }
        }

        // move from occupy able building
        if (isExistBuildingToOccupy) {
            const closeBuilding = getCloseBuildingListToOccupy(movedAction.moveFrom, buildingToOccupyList);

            if (closeBuilding && !unitInfo.occupyBuildingList.includes(closeBuilding.buildingType)) {
                // move from building
                return (
                    getLengthBetweenPoints(movedAction.moveTo, {x: closeBuilding.x, y: closeBuilding.y}) -
                    onBuildingStateRate -
                    occupyBuildingAdditionalRating
                );
            }
        }

        return (Math.random() - 0.5) * occupyBuildingAdditionalRating;
    });
}

function getRecommendedToBuyUnitType(gameState: GameStateType): UnitPriceItemType | null {
    const {activeGamePlayerId, playerList, unitList, buildingList} = gameState;

    const playerById = getPlayerByPlayerId(activeGamePlayerId, playerList);

    if (!playerById) {
        console.error(`can not get player by id: ${activeGamePlayerId}`);
        return null;
    }

    const playerUnitList = getUnitListByPlayerId(activeGamePlayerId, unitList);

    const soldierNeededCount: number = buildingList.reduce<number>(
        (count: number, building: GameBuildingType): number => {
            return count + (building.buildingType === GameBuildingTypeEnum.farm ? 1 : 0);
        },
        0
    );

    const soldierCount: number = playerUnitList.reduce<number>((count: number, unitInList: GameUnitType): number => {
        return count + (unitInList.unitType === GameUnitTypeEnum.soldier ? 1 : 0);
    }, 0);

    if (soldierCount < soldierNeededCount) {
        return {
            cost: getUnitInfoByType(GameUnitTypeEnum.soldier).cost,
            isHero: false,
            unitId: getRandomStringWithPrefix(GameUnitTypeEnum.soldier),
            unitType: GameUnitTypeEnum.soldier,
        };
    }

    const unitToBuyList: Array<UnitPriceItemType> = getUnitPriceList(
        activeGamePlayerId,
        availableToBuyUnitTypeList,
        []
    );

    if (unitToBuyList.length === 0) {
        return null;
    }

    const mostExpensiveUnit: UnitPriceItemType = unitToBuyList.reduce<UnitPriceItemType>(
        (current: UnitPriceItemType, value: UnitPriceItemType): UnitPriceItemType => {
            if (playerById.money >= value.cost && value.cost > current.cost) {
                return value;
            }
            return current;
        },
        unitToBuyList[0]
    );

    if (mostExpensiveUnit.unitType === GameUnitTypeEnum.soldier) {
        return null;
    }

    return mostExpensiveUnit;
}

// eslint-disable-next-line complexity, max-statements
function getBuyUnitMovedAction(gameState: GameStateType): MovedGameActionType | null {
    const {landscape, activeGamePlayerId, playerList, cemetery, unitList, gameSettings} = gameState;

    const playerById = getPlayerByPlayerId(activeGamePlayerId, playerList);

    if (!playerById) {
        console.error(`can not get player by id: ${activeGamePlayerId}`);
        return null;
    }

    const playerUnitList = getUnitListByPlayerId(activeGamePlayerId, unitList);

    if (playerUnitList.length >= gameSettings.unitLimit) {
        return null;
    }

    const storePointList: Array<PointType> = getLandscapePointList(landscape).filter<PointType>(
        (point: PointType): point is PointType => {
            return getCanOpenStoreByPoint(point, gameState);
        }
    );

    if (storePointList.length === 0) {
        return null;
    }

    const [storePoint] = storePointList;

    const unitToBuyList: Array<UnitPriceItemType> = getUnitPriceList(
        activeGamePlayerId,
        availableToBuyUnitTypeList,
        cemetery
    );

    const deadHeroList: Array<UnitPriceItemType> = [...unitToBuyList].filter(
        (unitPriceItem: UnitPriceItemType): unitPriceItem is UnitPriceItemType => {
            return unitPriceItem.isHero;
        }
    );

    const cheapHero: UnitPriceItemType | null = getMinInList<UnitPriceItemType>(
        deadHeroList,
        (heroPriceItem: UnitPriceItemType): number => {
            return heroPriceItem.cost;
        }
    );

    if (cheapHero && playerById.money >= cheapHero.cost) {
        return {
            gameAction: {
                actionType: buyUnit,
                addUnitList: [],
                buyUnitType: cheapHero.unitType,
                movePointList: [],
                toPoint: storePoint,
                unitCurrentPoint: storePoint,
                unitId: cheapHero.unitId,
            },
            moveFrom: storePoint,
            movePathList: [],
            moveTo: storePoint,
        };
    }

    const recommendedUnitToBuy: UnitPriceItemType | null = getRecommendedToBuyUnitType(gameState);

    if (recommendedUnitToBuy && playerById.money >= recommendedUnitToBuy.cost) {
        return {
            gameAction: {
                actionType: buyUnit,
                addUnitList: [],
                buyUnitType: recommendedUnitToBuy.unitType,
                movePointList: [],
                toPoint: storePoint,
                unitCurrentPoint: storePoint,
                unitId: getRandomStringWithPrefix(recommendedUnitToBuy.unitType),
            },
            moveFrom: storePoint,
            movePathList: [],
            moveTo: storePoint,
        };
    }

    return null;
}

// eslint-disable-next-line complexity, max-statements
export function getMaxAction(
    movedActionList: Array<MovedGameActionType>,
    gameState: GameStateType
): MovedGameActionType {
    const deniedMovePointList: Array<PointType> = getDeniedToMovePointList(gameState);
    const sortedMovedActionMap = getSortedMovedActionMap(movedActionList, deniedMovePointList, gameState.unitList);

    const buyUnitMovedAction = getBuyUnitMovedAction(gameState);

    if (buyUnitMovedAction) {
        return buyUnitMovedAction;
    }

    // occupy building is the most important deal, first!
    const occupyBuildingMovedAction = getOccupyBuildingMovedAction(sortedMovedActionMap);

    if (occupyBuildingMovedAction) {
        return occupyBuildingMovedAction;
    }

    const unitRaiseSkeletonMovedAction = getUnitRaiseSkeletonMovedAction(sortedMovedActionMap, gameState);

    if (unitRaiseSkeletonMovedAction) {
        return unitRaiseSkeletonMovedAction;
    }

    const unitToUnitAttackMovedAction = getUnitToUnitAttackMovedAction(sortedMovedActionMap, gameState);

    if (unitToUnitAttackMovedAction) {
        return unitToUnitAttackMovedAction;
    }

    const unitToBuildingAttackMovedAction = getUnitToBuildingAttackMovedAction(sortedMovedActionMap, gameState);

    if (unitToBuildingAttackMovedAction) {
        return unitToBuildingAttackMovedAction;
    }

    const unitFixBuildingMovedAction = getUnitFixBuildingMovedAction(sortedMovedActionMap);

    if (unitFixBuildingMovedAction) {
        return unitFixBuildingMovedAction;
    }

    const unitMoveMovedAction = getUnitMoveMovedAction(sortedMovedActionMap, gameState);

    if (unitMoveMovedAction) {
        return unitMoveMovedAction;
    }

    return {
        gameAction: {
            actionType: changeTurn,
            addUnitList: [],
            buyUnitType: null,
            movePointList: [],
            toPoint: defaultPoint,
            unitCurrentPoint: defaultPoint,
            unitId: '',
        },
        moveFrom: defaultPoint,
        movePathList: [],
        moveTo: defaultPoint,
    };
}
