import {ActionArgumentType, GameAnimationType} from '../game-context-type';
import {getNextPlayerId} from '../game-context-helper';
import {GameStateType} from '../../../model/game/game-type';
import {GameGraveType, GameUnitType} from '../../../model/game/unit/unit-type';
import {graveLiveCount} from '../../../page/client/client-game/client-game-grave/client-game-grave-helper';
import {getSwitchTurnUnitState} from '../../../model/game/unit/unit-state';
import {GamePlayerIdEnum, GamePlayerType} from '../../../model/game/player/player-type';
import {getPlayerByPlayerId, getPlayerTurnIncome} from '../../../model/game/player/player-helper';
import {PromiseResolveType} from '../../../util/promise';
import {waitForNextFrame} from '../../../util/time';

export function changeTurnAction(data: ActionArgumentType): Promise<void> {
    const {removeAnimationByAction, action, setAnimationList, setGameState, gameState, setIsOpenStore} = data;
    const {playerList, activeGamePlayerId, buildingList} = gameState;
    const newPlayerList: Array<GamePlayerType> = [...playerList];

    const nextActiveGamePlayerId: GamePlayerIdEnum = getNextPlayerId(activeGamePlayerId, newPlayerList);
    const nextPlayer: GamePlayerType | null = getPlayerByPlayerId(nextActiveGamePlayerId, newPlayerList);

    if (!nextPlayer) {
        console.error('cen not find next player');
        console.error(activeGamePlayerId, '->', nextActiveGamePlayerId);
        return waitForNextFrame();
    }

    nextPlayer.money += getPlayerTurnIncome(nextActiveGamePlayerId, buildingList);

    setIsOpenStore(false);

    // count additional healths for unit
    // count additional money for player
    // and set it into state
    setGameState((currentGameState: GameStateType): GameStateType => {
        const {stepCount, graveList, unitList} = currentGameState;
        const newStepCount = stepCount + 1;

        return {
            ...currentGameState,
            activeGamePlayerId: nextActiveGamePlayerId,
            graveList: graveList.filter<GameGraveType>(
                (grave: GameGraveType): grave is GameGraveType => grave.createdStepCount + graveLiveCount > newStepCount
            ),
            playerList: newPlayerList,
            stepCount: newStepCount,
            unitList: unitList.map<GameUnitType>((unit: GameUnitType): GameUnitType => {
                return getSwitchTurnUnitState(nextActiveGamePlayerId, unit, buildingList, playerList);
            }),
        };
    });

    return new Promise<void>((resolve: PromiseResolveType<void>) => {
        setAnimationList((animList: Array<GameAnimationType>) => {
            return [
                ...animList,
                {
                    action,
                    addHealthList: [],
                    animationOnFinish: async (): Promise<void> => {
                        removeAnimationByAction(action);
                        await waitForNextFrame();
                        resolve();
                    },
                },
            ];
        });
    });
}
