import {noop} from '../../util/function';

import {AlertProviderDataItemType} from './alert-type';

export function createDefaultAlertProviderData(index: number): AlertProviderDataItemType {
    return {
        header: 'UI__YES',
        index,
        isOpen: false,
        onOk: noop,
        text: 'UI__YES',
    };
}
