/* global HTMLImageElement, KeyframeAnimationOptions, Keyframe */

import {unitImage} from '../image/unit-image';
import {GamePlayerColorIdEnum, GamePlayerType} from '../../../../../model/game/player/player-type';
import {GameUnitType} from '../../../../../model/game/unit/unit-type';
import {getPlayerByPlayerId} from '../../../../../model/game/player/player-helper';

export function getImageListByUnit(
    unit: GameUnitType,
    playerList: Array<GamePlayerType>
): readonly [HTMLImageElement, HTMLImageElement] {
    const {gamePlayerId, unitType} = unit;
    const player: GamePlayerType | null = getPlayerByPlayerId(gamePlayerId, playerList);
    const color: GamePlayerColorIdEnum = player?.gamePlayerColorId || GamePlayerColorIdEnum.gray;

    return unitImage[unitType][color];
}

export const unitAnimationOption: KeyframeAnimationOptions = {
    easing: 'ease-in-out',
    fill: 'forwards',
    iterations: 1,
};

const unitAnimHealthKeyBegin: Keyframe = {opacity: 0, transform: 'translate3d(0px, 12px, 0)'};
const unitAnimHealthKeyEnd: Keyframe = {opacity: 1, transform: 'translate3d(0px, -12px, 0)'};

export const unitAddHealthList = [
    ...Array.from<Keyframe>({length: 1}).fill(unitAnimHealthKeyBegin),
    ...Array.from<Keyframe>({length: 3}).fill(unitAnimHealthKeyEnd),
    {...unitAnimHealthKeyEnd, opacity: 0},
];

export const levelUpKeyList = [
    ...Array.from<Keyframe>({length: 1}).fill(unitAnimHealthKeyBegin),
    ...Array.from<Keyframe>({length: 3}).fill(unitAnimHealthKeyEnd),
    {...unitAnimHealthKeyEnd, opacity: 0},
];
