import {Page} from '../../../layout/page/page';
import {mapList} from '../../../model/map/map-const';
import {GameMapType} from '../../../model/map/map-type';
import {UiHeader} from '../../../layout/ui-header/ui-header';

import clientSkirmishStyle from './client-skirmish.scss';
import {ClientSkirmishMapItem} from './client-skirmish-map-item/client-skirmish-map-item';

// eslint-disable-next-line max-statements
export function ClientSkirmish(): JSX.Element {
    return (
        <Page>
            <div className={clientSkirmishStyle.client_skirmish}>
                <UiHeader langKey="UI__BUTTON__SKIRMISH" />
                <div className={clientSkirmishStyle.client_skirmish_map_list}>
                    {mapList.map<JSX.Element>((map: GameMapType): JSX.Element => {
                        return <ClientSkirmishMapItem key={map.mapId} map={map} />;
                    })}
                </div>
            </div>
        </Page>
    );
}
