import {createContext, ReactNode, useCallback, useContext, useEffect, useMemo, useState} from 'react';

import {GameStateType, GetAllActionsArgType} from '../../model/game/game-type';
import {NeverError} from '../../util/error';
import {getLandscapeSize} from '../../model/game/landscape/landscape-helper';
import {PointType} from '../../model/game/point/point-type';
import {getUnitOverUnit} from '../../model/game/unit/unit-helper';
import {GameAllActionsType, GameUnitType} from '../../model/game/unit/unit-type';
import {SizeType} from '../../model/map/map-type';
import {getPointAvailableActionsData} from '../../model/game/unit/unit-action';
import {getIsSkirmishMap} from '../../model/map/map-helper';
import {skirmishGetPlayerListToRemove} from '../../model/game/trigger/trigger-check';
import {GamePlayerIdEnum, GamePlayerType, GamePlayerTypeEnum} from '../../model/game/player/player-type';
import {GameBuildingType} from '../../model/game/building/building-type';
import {getPlayerByPlayerId} from '../../model/game/player/player-helper';

import {waitForNextFrame} from '../../util/time';

import {defaultAvailableActions, defaultGameContext} from './game-context-const';
import {
    ActionArgumentType,
    GameActionType,
    GameActionTypeEnum,
    GameAnimationType,
    GameContextType,
} from './game-context-type';
import {unitMoveAction} from './action/unit-move-action';
import {changeTurnAction} from './action/change-turn-action';
import {raiseSkeletonAction} from './action/raise-skeleton-action';
import {unitToUnitAttackAction} from './action/unit-to-unit-attack-action';
import {unitToBuildingAttackAction} from './action/unit-to-building-attack-action';
import {occupyBuildingAction} from './action/occupy-building-action';
import {fixBuildingAction} from './action/fix-building-action';
import {addUnitListAction, buyUnitListAction, closeStoreAction, openStoreAction} from './action/store-action';
import {getDefaultGameState} from './game-context-helper';

export const GameContext = createContext<GameContextType>(defaultGameContext);

export function useGameContext(): GameContextType {
    return useContext<GameContextType>(GameContext);
}

const {Provider: GameContextProvider} = GameContext;

type GameProviderPropsType = {
    children: ReactNode;
};

// eslint-disable-next-line max-statements
export function GameProvider(props: GameProviderPropsType): JSX.Element {
    const {children} = props;
    const [isOpenStore, setIsOpenStore] = useState<boolean>(false);
    const [availableActions, setAvailableActions] = useState<GameAllActionsType>(defaultAvailableActions);
    const [activePoint, setActivePoint] = useState<PointType>(defaultGameContext.activePoint);
    const [gameState, setGameState] = useState<GameStateType>(getDefaultGameState());
    const [animationList, setAnimationList] = useState<Array<GameAnimationType>>([]);
    const [localAnimationList, setLocalAnimationList] = useState<Set<string>>(new Set<string>());
    const {height, width} = getLandscapeSize(gameState.landscape);
    const mapSize: SizeType = useMemo((): SizeType => {
        return {height, width};
    }, [height, width]);
    const [isGameRunning, setIsGameRunning] = useState<boolean>(false);
    const [lastMoveAction, setLastMoveAction] = useState<GameActionType | null>(null);

    const unitOverUnit: [GameUnitType, GameUnitType] | null = useMemo((): [GameUnitType, GameUnitType] | null => {
        return getUnitOverUnit(gameState.unitList);
    }, [gameState.unitList]);

    // eslint-disable-next-line complexity
    const isEnableEndTurnButton: boolean = useMemo<boolean>((): boolean => {
        const player = getPlayerByPlayerId(gameState.activeGamePlayerId, gameState.playerList);

        if (!player) {
            return false;
        }

        return (
            unitOverUnit === null &&
            // have NO global animations\actions
            animationList.length === 0 &&
            // have NO local animations
            localAnimationList.size === 0 &&
            // the current player is human
            player.playerType === GamePlayerTypeEnum.human &&
            // the current player plays on current device
            gameState.clientGamePlayerIdList.includes(player.gamePlayerId)
        );
    }, [
        unitOverUnit,
        animationList.length,
        gameState.activeGamePlayerId,
        gameState.clientGamePlayerIdList,
        gameState.playerList,
        localAnimationList.size,
    ]);

    const removeAnimationByAction = useCallback((action: GameActionType) => {
        setAnimationList((currentAnimationList: Array<GameAnimationType>) => {
            return currentAnimationList.filter<GameAnimationType>(
                (animation: GameAnimationType): animation is GameAnimationType => animation.action !== action
            );
        });
    }, []);

    const clearAvailableActions = useCallback(() => {
        setAvailableActions(defaultAvailableActions);
    }, []);

    const pushAction = useCallback(
        // eslint-disable-next-line complexity, max-statements
        function pushAction(action: GameActionType): Promise<void> {
            const actionArgument: ActionArgumentType = {
                action,
                gameState,
                lastMoveAction,
                removeAnimationByAction,
                setAnimationList,
                setGameState,
                setIsOpenStore,
                setLastMoveAction,
            };

            console.log('--- pushAction ---');
            console.log(action);

            if (action.actionType === GameActionTypeEnum.unitMove) {
                setLastMoveAction(action);
            } else {
                setLastMoveAction(null);
            }

            switch (action.actionType) {
                case GameActionTypeEnum.changeTurn: {
                    clearAvailableActions();
                    return changeTurnAction(actionArgument);
                }
                case GameActionTypeEnum.raiseSkeleton: {
                    return raiseSkeletonAction(actionArgument);
                }
                case GameActionTypeEnum.unitMove:
                case GameActionTypeEnum.unitMoveRevert: {
                    return unitMoveAction(actionArgument);
                }
                case GameActionTypeEnum.unitToUnitAttack: {
                    return unitToUnitAttackAction(actionArgument);
                }
                case GameActionTypeEnum.unitToBuildingAttack: {
                    return unitToBuildingAttackAction(actionArgument);
                }
                case GameActionTypeEnum.occupyBuilding: {
                    return occupyBuildingAction(actionArgument);
                }
                case GameActionTypeEnum.fixBuilding: {
                    return fixBuildingAction(actionArgument);
                }
                case GameActionTypeEnum.openStore: {
                    clearAvailableActions();
                    return openStoreAction(actionArgument);
                }
                case GameActionTypeEnum.closeStore: {
                    return closeStoreAction(actionArgument);
                }
                case GameActionTypeEnum.addUnitList: {
                    return addUnitListAction(actionArgument);
                }
                case GameActionTypeEnum.buyUnit: {
                    return buyUnitListAction(actionArgument);
                }
                default: {
                    throw new NeverError(action.actionType);
                }
            }
        },
        [lastMoveAction, gameState, removeAnimationByAction, clearAvailableActions]
    );

    // remove defeated player, check skirmish states
    useEffect(() => {
        if (!isGameRunning) {
            return;
        }

        const isSkirmish = getIsSkirmishMap(gameState.mapId);

        if (!isSkirmish) {
            return;
        }

        const playerIdToRemoveList = skirmishGetPlayerListToRemove(gameState);

        if (playerIdToRemoveList.length === 0) {
            return;
        }

        const {unitList, playerList, buildingList} = gameState;
        const newUnitList: Array<GameUnitType> = [...unitList].map<GameUnitType>((unit: GameUnitType): GameUnitType => {
            if (playerIdToRemoveList.includes(unit.gamePlayerId)) {
                return {
                    ...unit,
                    gamePlayerId: GamePlayerIdEnum.playerEmpty,
                };
            }

            return unit;
        });
        const newBuildingList: Array<GameBuildingType> = [...buildingList].map<GameBuildingType>(
            (building: GameBuildingType): GameBuildingType => {
                if (playerIdToRemoveList.includes(building.gamePlayerId)) {
                    return {
                        ...building,
                        gamePlayerId: GamePlayerIdEnum.playerEmpty,
                    };
                }

                return building;
            }
        );

        const newPlayerList: Array<GamePlayerType> = [...playerList].filter<GamePlayerType>(
            (player: GamePlayerType): player is GamePlayerType => {
                return !playerIdToRemoveList.includes(player.gamePlayerId);
            }
        );

        setGameState({
            ...gameState,
            buildingList: newBuildingList,
            playerList: newPlayerList,
            unitList: newUnitList,
        });
    }, [gameState, isGameRunning]);

    const resetState = useCallback(() => {
        setLastMoveAction(null);
        setIsGameRunning(false);
        setLocalAnimationList(new Set<string>());
        setAnimationList([]);
        setActivePoint(defaultGameContext.activePoint);
        setGameState(getDefaultGameState());
        setAvailableActions(defaultAvailableActions);
    }, []);

    const loadGame = useCallback(
        async (gameStateToLoad: GameStateType): Promise<void> => {
            resetState();
            await waitForNextFrame();
            setGameState(gameStateToLoad);
            setIsGameRunning(true);
        },
        [resetState]
    );

    const providedData: GameContextType = useMemo<GameContextType>((): GameContextType => {
        const endAvailableActions = unitOverUnit
            ? getPointAvailableActionsData(
                  {
                      activeGamePlayerId: gameState.activeGamePlayerId,
                      clientGamePlayerIdList: gameState.clientGamePlayerIdList,
                      point: {x: unitOverUnit[1].x, y: unitOverUnit[1].y},
                  },
                  gameState
              )
            : availableActions;

        return {
            activePoint,
            animationList,
            availableActions: endAvailableActions,
            gameState,
            getAvailableActions: (data: GetAllActionsArgType): GameAllActionsType =>
                getPointAvailableActionsData(data, gameState),
            isEnableEndTurnButton,
            isGameRunning,
            isOpenStore,
            lastMoveAction,
            loadGame,
            localAnimationList,
            mapSize,
            pushAction,
            resetState,
            setActivePoint,
            setAvailableActions,
            setGameState,
            setIsGameRunning,
            setIsOpenStore,
            setLocalAnimationList,
            unitOverUnit,
        };
    }, [
        lastMoveAction,
        loadGame,
        isOpenStore,
        resetState,
        isEnableEndTurnButton,
        localAnimationList,
        pushAction,
        isGameRunning,
        gameState,
        mapSize,
        animationList,
        availableActions,
        activePoint,
        unitOverUnit,
    ]);

    console.log('==== GameContext ====');
    console.log(providedData);

    return <GameContextProvider value={providedData}>{children}</GameContextProvider>;
}
