import {createContext, ReactNode, useCallback, useMemo, useState, useContext} from 'react';

import {GamePopup} from '../../layout/game-popup/game-popup';
import gamePopupStyle from '../../layout/game-popup/game-popup.scss';
import {Locale} from '../locale/locale-context';
import {UiButton} from '../../layout/ui-button/ui-button';

import {AlertArgumentType, AlertContextType, AlertProviderDataItemType} from './alert-type';
import {defaultAlertContextData} from './alert-const';
import {createDefaultAlertProviderData} from './alert-helper';

export const AlertContext = createContext<AlertContextType>(defaultAlertContextData);

export function useAlertContext(): AlertContextType {
    return useContext<AlertContextType>(AlertContext);
}

const {Provider: AlertContextProvider} = AlertContext;

type AlertContextPropsType = {
    children: ReactNode;
};

export function AlertProvider(props: AlertContextPropsType): JSX.Element {
    const [alertDataList, setAlertDataList] = useState<Array<AlertProviderDataItemType>>([
        createDefaultAlertProviderData(0),
        createDefaultAlertProviderData(1),
        createDefaultAlertProviderData(2),
    ]);

    const {children} = props;

    const closePopupByIndex = useCallback((index: number) => {
        setAlertDataList((currentAlertDataList: Array<AlertProviderDataItemType>): Array<AlertProviderDataItemType> => {
            const newAlertDataList: Array<AlertProviderDataItemType> = [...currentAlertDataList];

            newAlertDataList[index] = {
                ...newAlertDataList[index],
                isOpen: false,
            };

            return newAlertDataList;
        });
    }, []);

    const providedData: AlertContextType = useMemo(() => {
        return {
            alert: (data: AlertArgumentType) => {
                setAlertDataList(
                    (currentAlertDataList: Array<AlertProviderDataItemType>): Array<AlertProviderDataItemType> => {
                        const newAlertDataList: Array<AlertProviderDataItemType> = [...currentAlertDataList];

                        newAlertDataList[data.index] = {
                            ...data,
                            isOpen: true,
                        };

                        return newAlertDataList;
                    }
                );
            },
        };
    }, []);

    return (
        <AlertContextProvider value={providedData}>
            {children}
            {alertDataList.map((alertData: AlertProviderDataItemType, index: number): JSX.Element => {
                return (
                    <GamePopup isOpen={alertData.isOpen} key={String(index).trim()}>
                        {alertData.header ? (
                            <h3 className={gamePopupStyle.game_popup__header}>
                                <Locale stringKey={alertData.header} />
                            </h3>
                        ) : null}
                        {alertData.text ? (
                            <p className={gamePopupStyle.game_popup__text}>
                                <Locale stringKey={alertData.text} />
                            </p>
                        ) : null}
                        <div className={gamePopupStyle.game_popup__footer}>
                            <UiButton
                                langKey="UI__OK"
                                onClick={() => {
                                    if (alertData.onOk) {
                                        alertData.onOk();
                                    }
                                    closePopupByIndex(index);
                                }}
                            />
                        </div>
                    </GamePopup>
                );
            })}
        </AlertContextProvider>
    );
}
