import {PointType} from '../point/point-type';
import {GamePlayerIdEnum} from '../player/player-type';

import {BuildingInfoType, GameBuildingStateEnum, GameBuildingType, GameBuildingTypeEnum} from './building-type';
import {buildingInfoMap} from './building-const';

export function getBuildingByType(buildingType: GameBuildingTypeEnum): BuildingInfoType {
    return buildingInfoMap[buildingType];
}

export function getBuildingInfo(building: GameBuildingType): BuildingInfoType {
    return buildingInfoMap[building.buildingType];
}

export function getBuildingByPoint(point: PointType, buildingList: Array<GameBuildingType>): GameBuildingType | null {
    return (
        buildingList.find<GameBuildingType>(
            (building: GameBuildingType): building is GameBuildingType =>
                building.x === point.x && building.y === point.y
        ) || null
    );
}

export function getBuildingListByPlayerId(
    playerId: GamePlayerIdEnum,
    buildingList: Array<GameBuildingType>
): Array<GameBuildingType> {
    return buildingList.filter<GameBuildingType>((building: GameBuildingType): building is GameBuildingType => {
        if (building.gamePlayerId !== playerId) {
            return false;
        }

        return building.state === GameBuildingStateEnum.buildingOk;
    });
}

export function getStoreListByPlayerId(
    playerId: GamePlayerIdEnum,
    buildingList: Array<GameBuildingType>
): Array<GameBuildingType> {
    return getBuildingListByPlayerId(playerId, buildingList).filter<GameBuildingType>(
        (building: GameBuildingType): building is GameBuildingType => {
            const {canBeStore} = getBuildingInfo(building);

            return canBeStore;
        }
    );
}

export function getHasPlayerStore(playerId: GamePlayerIdEnum, buildingList: Array<GameBuildingType>): boolean {
    return getStoreListByPlayerId(playerId, buildingList).length > 0;
}
