import {useGameContext} from '../../../../provider/game/game-context';

import clientGameLandscapeStyle from './client-game-landscape.scss';
import {getLandscapeCanvas} from './client-game-landscape-canvas';

export function ClientGameLandscape(): JSX.Element {
    const {gameState, mapSize} = useGameContext();
    const {landscape, buildingList} = gameState;

    return (
        <div className={clientGameLandscapeStyle.landscape_layer}>
            <img
                alt="map"
                className={clientGameLandscapeStyle.landscape_layer_image}
                src={getLandscapeCanvas(mapSize, landscape, buildingList)}
            />
        </div>
    );
}
