export const levelUnitData = {
    additionalArmor: 5,
    additionalAttack: 5,
    base: 100,
    experience: {
        destroyBuilding: 60,
    },
    max: 9,
    min: 0,
} as const;

export type GameUnitLevelDataType = {
    experience: {
        from: number;
        part: number;
        percent: number;
        size: number;
        to: number;
        value: number;
    };
    level: number;
};

export function getUnitLevelData(damageGiven: number): GameUnitLevelDataType {
    const experience = damageGiven;
    const maxLevel = levelUnitData.max;
    const levelBase = levelUnitData.base;

    let experienceFrom = 0;
    let experienceTo = 0;

    // eslint-disable-next-line no-loops/no-loops
    for (let level = 0; level < maxLevel; level += 1) {
        experienceFrom = ((level * (level + 1)) / 2) * levelBase;
        experienceTo = (((level + 1) * (level + 2)) / 2) * levelBase;

        if (experience >= experienceFrom && experience < experienceTo) {
            return {
                experience: {
                    from: experienceFrom,
                    part: experience - experienceFrom,
                    percent: ((experience - experienceFrom) / (experienceTo - experienceFrom)) * 100,
                    size: experienceTo - experienceFrom,
                    to: experienceTo,
                    value: experience,
                },
                level,
            };
        }
    }

    experienceFrom = (((maxLevel - 1) * maxLevel) / 2) * levelBase;
    experienceTo = ((maxLevel * (maxLevel + 1)) / 2) * levelBase;

    console.log('--- max level ---');

    return {
        experience: {
            from: experienceFrom,
            part: experience - experienceFrom,
            percent: ((experience - experienceFrom) / (experienceTo - experienceFrom)) * 100,
            size: experienceTo - experienceFrom,
            to: experienceTo,
            value: experience,
        },
        level: levelUnitData.max,
    };
}
