/* global document, HTMLDivElement */
import {ReactNode, useRef} from 'react';
import {useSpring, animated} from '@react-spring/web';
import {createUseGesture, dragAction, pinchAction} from '@use-gesture/react';
import type {FullGestureState, CommonGestureState} from '@use-gesture/core/types';

import {useGameContext} from '../../../../provider/game/game-context';
import {usePreventGesture} from '../../../../util/use-prevent-gesture';

import clientGameViewContainerStyle from './client-game-view-container.scss';
import {squareSize} from './client-game-view-container-const';

const useGesture = createUseGesture([dragAction, pinchAction]);

type ClientGameViewContainerPropsType = {
    children: ReactNode;
};

export function ClientGameViewContainer(props: ClientGameViewContainerPropsType): JSX.Element {
    const {children} = props;
    const {mapSize} = useGameContext();

    usePreventGesture();

    const [stringStyle, stringApi] = useSpring(() => ({
        rotateZ: 0,
        scale: 2,
        x: 0,
        y: 0,
    }));

    const gestureDivRef = useRef<HTMLDivElement>(document.createElement('div'));

    useGesture(
        {
            onDrag: (onDragData: Omit<FullGestureState<'drag'>, 'event'>) => {
                const {
                    pinching,
                    cancel,
                    offset: [offsetX, offsetY],
                } = onDragData;

                if (pinching) {
                    cancel();
                    return;
                }

                stringApi.start({x: Math.round(offsetX), y: Math.round(offsetY)});
            },

            // Array<number> try to type onPinchData.memo
            onPinch: (onPinchData: Omit<FullGestureState<'pinch'>, 'event'>): Array<number> => {
                const {
                    origin: [originX, originY],
                    first,
                    movement: [milliseconds],
                    offset: [
                        scale,
                        // rotateZ
                    ],
                    // type-coverage:ignore-next-line
                    memo,
                } = onPinchData;

                let onPinchDataMemo: Array<number> = memo || [0, 0, 0, 0];

                if (first) {
                    const {
                        width: rectWidth,
                        height: rectHeight,
                        x: rectX,
                        y: rectY,
                    } = gestureDivRef.current.getBoundingClientRect();
                    const translateX = originX - (rectX + rectWidth / 2);
                    const translateY = originY - (rectY + rectHeight / 2);

                    onPinchDataMemo = [stringStyle.x.get(), stringStyle.y.get(), translateX, translateY];
                }

                const x = onPinchDataMemo[0] - (milliseconds - 1) * onPinchDataMemo[2];
                const y = onPinchDataMemo[1] - (milliseconds - 1) * onPinchDataMemo[3];

                stringApi.start({
                    rotateZ: 0,
                    scale,
                    x: Math.round(x),
                    y: Math.round(y),
                });

                return onPinchDataMemo;
            },
        },
        {
            drag: {
                bounds: (gestureState: CommonGestureState | void) => {
                    console.log('gestureState', gestureState);
                    const scale: number = stringStyle.scale.get();
                    const bottomLimit = Math.round(((mapSize.height * squareSize) / 2) * scale);
                    const rightLimit = Math.round(((mapSize.width * squareSize) / 2) * scale);

                    return {
                        bottom: bottomLimit,
                        left: -rightLimit,
                        right: rightLimit,
                        top: -bottomLimit,
                    };
                },
                from: () => [stringStyle.x.get(), stringStyle.y.get()],
                rubberband: true,
            },
            pinch: {
                pinchOnWheel: true,
                preventScroll: true,
                scaleBounds: {max: 4, min: 1},
            },
            target: gestureDivRef,
        }
    );

    return (
        <div className={clientGameViewContainerStyle.client_game_view_container} ref={gestureDivRef}>
            <animated.div
                className={clientGameViewContainerStyle.client_game_view_container__animated_div}
                style={stringStyle}
            >
                <div
                    className={clientGameViewContainerStyle.child_wrapper}
                    style={{height: `${mapSize.height * squareSize}px`, width: `${mapSize.width * squareSize}px`}}
                >
                    {children}
                </div>
            </animated.div>
        </div>
    );
}
