import {GameBuildingType} from '../building/building-type';

import {PointType} from './point-type';

export function getPointByPoint(pointToFind: PointType, pointList: Array<PointType>): PointType | null {
    return (
        pointList.find<PointType>(
            (pointInList: PointType): pointInList is GameBuildingType =>
                pointInList.x === pointToFind.x && pointInList.y === pointToFind.y
        ) || null
    );
}

export function getIsPointInList(pointToFind: PointType, pointList: Array<PointType>): boolean {
    return pointList.some(
        (pointInList: PointType): boolean => pointInList.x === pointToFind.x && pointInList.y === pointToFind.y
    );
}

export function getIsEqualPoint(pointA: PointType, pointB: PointType): boolean {
    return pointA.x === pointB.x && pointA.y === pointB.y;
}

export function getLengthBetweenPoints(pointA: PointType, pointB: PointType): number {
    return Math.sqrt((pointA.x - pointB.x) ** 2 + (pointA.y - pointB.y) ** 2);
}
