/* global HTMLSelectElement */

import {SyntheticEvent, Dispatch, SetStateAction} from 'react';

import {getEnumValue} from '../../../../util/enum';
import {GamePlayerTeamIdEnum, GamePlayerType, GamePlayerTypeEnum} from '../../../../model/game/player/player-type';
import {gamePlayerTeamIdList} from '../../../../model/game/player/player-const';
import {Locale} from '../../../../provider/locale/locale-context';

import {colorImage, colorImageKeyLangMap} from './image/image-color';
import clientLocalGameSetupPlayerStyle from './client-local-game-setup-player.scss';

type ClientLocalGameSetupPlayerType = {
    player: GamePlayerType;
    setPlayerList: Dispatch<SetStateAction<Array<GamePlayerType>>>;
};

export function ClientLocalGameSetupPlayer(props: ClientLocalGameSetupPlayerType): JSX.Element {
    const {player, setPlayerList} = props;
    const {gamePlayerColorId, gamePlayerTeamId, playerType} = player;

    return (
        <div className={clientLocalGameSetupPlayerStyle.client_local_game_setup_player}>
            <div className={clientLocalGameSetupPlayerStyle.client_local_game_setup_player__left}>
                <img
                    alt={gamePlayerColorId}
                    className={clientLocalGameSetupPlayerStyle.client_local_game_setup_player__color_point}
                    src={colorImage[gamePlayerColorId].src}
                />
                <p className={clientLocalGameSetupPlayerStyle.client_local_game_setup_player__color_name}>
                    <Locale stringKey={colorImageKeyLangMap[gamePlayerColorId]} />
                </p>
            </div>

            <div className={clientLocalGameSetupPlayerStyle.client_local_game_setup_player__right}>
                {/* eslint-disable-next-line jsx-a11y/no-onchange */}
                <select
                    className={clientLocalGameSetupPlayerStyle.client_local_game_setup__select}
                    defaultValue={playerType}
                    onChange={(evt: SyntheticEvent<HTMLSelectElement>) => {
                        const newPlayerType = getEnumValue<GamePlayerTypeEnum>(
                            GamePlayerTypeEnum,
                            evt.currentTarget.value
                        );

                        if (!newPlayerType) {
                            console.error('Can not change player type');
                            return;
                        }

                        setPlayerList((currentPlayerList: Array<GamePlayerType>): Array<GamePlayerType> => {
                            const newPlayerList = [...currentPlayerList];

                            newPlayerList[newPlayerList.indexOf(player)] = {
                                ...player,
                                playerType: newPlayerType,
                            };
                            return newPlayerList;
                        });
                    }}
                >
                    <option value={GamePlayerTypeEnum.human}>
                        <Locale stringKey="SETUP_SKIRMISH__PLAYER" />
                    </option>
                    <option value={GamePlayerTypeEnum.bot}>
                        <Locale stringKey="SETUP_SKIRMISH__BOT" />
                    </option>
                    <option value={GamePlayerTypeEnum.none}>
                        <Locale stringKey="SETUP_SKIRMISH__NONE" />
                    </option>
                </select>

                {/* eslint-disable-next-line jsx-a11y/no-onchange */}
                <select
                    className={clientLocalGameSetupPlayerStyle.client_local_game_setup__select}
                    defaultValue={gamePlayerTeamId}
                    onChange={(evt: SyntheticEvent<HTMLSelectElement>) => {
                        const newPlayerTeamId = getEnumValue<GamePlayerTeamIdEnum>(
                            GamePlayerTeamIdEnum,
                            evt.currentTarget.value
                        );

                        if (!newPlayerTeamId) {
                            console.error('Can not change player team id');
                            return;
                        }

                        setPlayerList((currentPlayerList: Array<GamePlayerType>): Array<GamePlayerType> => {
                            const newPlayerList = [...currentPlayerList];

                            newPlayerList[newPlayerList.indexOf(player)] = {
                                ...player,
                                gamePlayerTeamId: newPlayerTeamId,
                            };
                            return newPlayerList;
                        });
                    }}
                >
                    {gamePlayerTeamIdList.map<JSX.Element>(
                        (teamId: GamePlayerTeamIdEnum, index: number): JSX.Element => {
                            return (
                                <option key={teamId} value={teamId}>
                                    <Locale stringKey="SETUP_SKIRMISH__TEAM" valueMap={{number: index + 1}} />
                                </option>
                            );
                        }
                    )}
                </select>
            </div>
        </div>
    );
}
