import {squareSize} from '../client-game-view-container/client-game-view-container-const';
import {GameBuildingType, GameBuildingTypeEnum} from '../../../../model/game/building/building-type';
import {GamePlayerIdEnum} from '../../../../model/game/player/player-type';

import clientGameBuildingEffectStyle from './client-game-building-effect.scss';
import {buildingEffectImage} from './image/building-effect-image';

type ClientGameBuildingItemPropsType = {
    building: GameBuildingType;
};

export function ClientGameBuildingEffectItem(props: ClientGameBuildingItemPropsType): JSX.Element {
    const {building} = props;
    const {buildingType, x, y, gamePlayerId} = building;
    const key = `${x}-${y}`;

    const isFarmSmoke: boolean =
        buildingType === GameBuildingTypeEnum.farm && gamePlayerId !== GamePlayerIdEnum.playerEmpty;

    const animationDelay = `${Math.random()}s`;

    if (isFarmSmoke) {
        return (
            <div
                className={clientGameBuildingEffectStyle.building_effect_item_farm_smoke}
                key={key}
                style={{
                    animationDelay,
                    left: x * squareSize,
                    top: y * squareSize - squareSize / 2,
                    zIndex: y,
                }}
            >
                <img
                    alt="smoke"
                    className={clientGameBuildingEffectStyle.building_effect_item_farm_smoke_image}
                    src={buildingEffectImage.smoke.src}
                    style={{animationDelay}}
                />
            </div>
        );
    }

    return <div key={key} />;

    /*
    return (
        <img
            alt={buildingType}
            className={clientGameBuildingEffectStyle}
            key={`${x}-${y}`}
            src={getBuildingImage(building, playerList)}
            style={{
                left: x * squareSize,
                top: y * squareSize + deltaY,
            }}
        />
    );
*/
}
