import {GameStateType} from '../game-type';
import {PointType} from '../point/point-type';
import {getLandscapeSize, getSquareArmorByPoint, getUnitBonusArmorByMoveType} from '../landscape/landscape-helper';
import {getIsEqualPoint, getIsPointInList} from '../point/point-helper';

import {SizeType} from '../../map/map-type';

import {
    defaultUnitData,
    unitUnderPoisonArmorReduce,
    unitUnderPoisonAttackReduce,
    unitUnderWispAuraArmorBonus,
    unitUnderWispAuraAttackBonus,
} from './unit-const';
import {GameUnitType, GameUnitTypeEnum} from './unit-type';
import {getMaxSquareByMoveSize} from './unit-path';
import {getUnitLevelData, levelUnitData} from './unit-level';
import {getIsUnitPoisoned, getIsUnitUnderWispAura} from './unit-state';
import {UnitToUnitAttackResultType} from './unit-to-unit-attack-type';
import {getUnitInfoByUnit} from './unit-helper';

// eslint-disable-next-line complexity
export function getUnitAllAvailableAttackPointList(unit: GameUnitType, gameState: GameStateType): Array<PointType> {
    const {x, y, didAttackUnit, didMove, didRaiseSkeleton, didFixBuilding, didOccupyBuilding, didAttackBuilding} = unit;
    const {attack, canAttackAfterMove, canMeleeAttack} = getUnitInfoByUnit(unit);
    const {landscape} = gameState;
    const mapSize: SizeType = getLandscapeSize(landscape);

    if (didAttackUnit || didFixBuilding || didOccupyBuilding || didAttackBuilding || didRaiseSkeleton) {
        return [];
    }

    if (didMove && canAttackAfterMove === false) {
        return [];
    }

    const maxPointList = getMaxSquareByMoveSize({x, y}, mapSize, attack.range);

    if (canMeleeAttack) {
        return maxPointList;
    }

    const meleePointList: Array<PointType> = [
        {x: x + 1, y},
        {x: x - 1, y},
        {x, y: y + 1},
        {x, y: y - 1},
    ];

    return maxPointList.filter<PointType>((point: PointType): point is PointType => {
        return !getIsPointInList(point, meleePointList);
    });
}

export function getBonusArmorByLevel(damageGiven: number): number {
    return getUnitLevelData(damageGiven).level * levelUnitData.additionalArmor;
}

export function getBonusAttackByLevel(damageGiven: number): number {
    return getUnitLevelData(damageGiven).level * levelUnitData.additionalAttack;
}

export function getUnitArmorValue(unit: GameUnitType, gameState: GameStateType): number {
    const {landscape, buildingList, playerList, stepCount, unitList} = gameState;
    const {x, y, damageGiven} = unit;
    const unitInfo = getUnitInfoByUnit(unit);

    const defaultArmorByPlace: number = getSquareArmorByPoint({x, y}, landscape, buildingList);
    const bonusArmorByPlaceAndMoveType: number = getUnitBonusArmorByMoveType(unit, landscape, buildingList);
    const defaultUnitArmor: number = unitInfo.armor;
    const isUnderPoison = getIsUnitPoisoned(unit, stepCount);
    const isUnderWispAura = getIsUnitUnderWispAura(unit, playerList, unitList, getLandscapeSize(landscape));
    const bonusArmorByLevel = getBonusArmorByLevel(damageGiven);

    return (
        defaultArmorByPlace +
        bonusArmorByPlaceAndMoveType +
        defaultUnitArmor +
        (isUnderWispAura ? unitUnderWispAuraArmorBonus : 0) +
        bonusArmorByLevel +
        (isUnderPoison ? -unitUnderPoisonArmorReduce : 0)
    );
}

export function getUnitAttackValue(
    units: Record<'aggressor' | 'defender', GameUnitType>,
    gameState: GameStateType
): number {
    const {aggressor, defender} = units;
    const {landscape, playerList, stepCount, unitList} = gameState;
    const aggressorUnitInfo = getUnitInfoByUnit(aggressor);
    const defenderUnitInfo = getUnitInfoByUnit(defender);

    // TODO: add Math.random() * instead of 0.5 *
    const unitSelfAttack =
        0.5 * (aggressorUnitInfo.attack.max - aggressorUnitInfo.attack.min) + aggressorUnitInfo.attack.min;
    const bonusAttackByLevel = getBonusAttackByLevel(aggressor.damageGiven);
    const isUnderWispAura = getIsUnitUnderWispAura(aggressor, playerList, unitList, getLandscapeSize(landscape));
    const isUnderPoison = getIsUnitPoisoned(aggressor, stepCount);
    const bonusAttackByMoveType = aggressorUnitInfo.bonusAttackVersus[defenderUnitInfo.moveType];
    const bonusAttackVersusSkeleton =
        defender.unitType === GameUnitTypeEnum.skeleton ? aggressorUnitInfo.bonusAttackVersusSkeleton : 0;

    return (
        unitSelfAttack +
        bonusAttackByLevel +
        (isUnderWispAura ? unitUnderWispAuraAttackBonus : 0) +
        (isUnderPoison ? -unitUnderPoisonAttackReduce : 0) +
        bonusAttackByMoveType +
        bonusAttackVersusSkeleton
    );
}

export function getUnitToUnitAttackedUnit(
    units: Record<'aggressor' | 'defender', GameUnitType>,
    gameState: GameStateType
): GameUnitType {
    const minDamage = 1;

    const {aggressor, defender} = units;
    const aggressorUnitInfo = getUnitInfoByUnit(aggressor);
    const {stepCount} = gameState;
    const defenderArmor = getUnitArmorValue(defender, gameState);
    const aggressorAttack = getUnitAttackValue({aggressor, defender}, gameState);

    const aggressorAttackScale = aggressor.health / defaultUnitData.health;
    const damage = Math.max(Math.floor((aggressorAttack - defenderArmor) * aggressorAttackScale), minDamage);

    return {
        ...defender,
        health: defender.health - damage,
        poisonTurnCount:
            aggressorUnitInfo.attackPoisonCount > 0
                ? stepCount + aggressorUnitInfo.attackPoisonCount
                : defender.poisonTurnCount,
    };
}

function getCanAttackByDistance(units: Record<'fromUnit' | 'toUnit', GameUnitType>, mapSize: SizeType): boolean {
    const {fromUnit, toUnit} = units;
    const fromUnitInfo = getUnitInfoByUnit(fromUnit);
    const toUnitPoint: PointType = {x: toUnit.x, y: toUnit.y};
    const attackPointList: Array<PointType> = getMaxSquareByMoveSize(
        {x: fromUnit.x, y: fromUnit.y},
        mapSize,
        fromUnitInfo.attack.range
    );

    if (fromUnitInfo.canMeleeAttack) {
        return attackPointList.some((attackPoint: PointType): boolean => {
            return getIsEqualPoint(attackPoint, toUnitPoint);
        });
    }

    const meleePointList: Array<PointType> = [
        {x: fromUnit.x + 1, y: fromUnit.y},
        {x: fromUnit.x - 1, y: fromUnit.y},
        {x: fromUnit.x, y: fromUnit.y + 1},
        {x: fromUnit.x, y: fromUnit.y - 1},
    ];

    return attackPointList
        .filter<PointType>((point: PointType): point is PointType => {
            return !getIsPointInList(point, meleePointList);
        })
        .some((attackPoint: PointType): boolean => {
            return getIsEqualPoint(attackPoint, toUnitPoint);
        });
}

export function getUnitToUnitAttackResult(
    units: Record<'aggressor' | 'defender', GameUnitType>,
    gameState: GameStateType
): UnitToUnitAttackResultType {
    const {aggressor, defender} = units;
    const {landscape} = gameState;

    const hasDefenderStrikeBackByDistance = getCanAttackByDistance(
        {fromUnit: defender, toUnit: aggressor},
        getLandscapeSize(landscape)
    );

    const defenderUnitAfterAttack: GameUnitType = getUnitToUnitAttackedUnit({aggressor, defender}, gameState);

    if (defenderUnitAfterAttack.health <= 0 || hasDefenderStrikeBackByDistance === false) {
        return {
            aggressor: {
                ...aggressor,
                damageGiven: aggressor.damageGiven + (defender.health - defenderUnitAfterAttack.health),
                didAttackUnit: true,
            },
            defender: {
                ...defenderUnitAfterAttack,
                damageReceived:
                    defenderUnitAfterAttack.damageReceived + (defender.health - defenderUnitAfterAttack.health),
            },
            hasStrikeBack: false,
        };
    }

    const aggressorStateAfterStrikeBack: GameUnitType = getUnitToUnitAttackedUnit(
        {aggressor: defenderUnitAfterAttack, defender: aggressor},
        gameState
    );

    return {
        aggressor: {
            ...aggressorStateAfterStrikeBack,
            damageGiven: aggressor.damageGiven + (defender.health - defenderUnitAfterAttack.health),
            damageReceived: aggressor.damageReceived + (aggressor.health - aggressorStateAfterStrikeBack.health),
            didAttackUnit: true,
        },
        defender: {
            ...defenderUnitAfterAttack,
            damageGiven: defender.damageGiven + (aggressor.health - aggressorStateAfterStrikeBack.health),
            damageReceived: defender.damageReceived + (defender.health - defenderUnitAfterAttack.health),
        },
        hasStrikeBack: true,
    };
}
