/* global document, HTMLImageElement, CanvasRenderingContext2D */

import {PointType} from '../../../../model/game/point/point-type';
import {squareSize} from '../client-game-view-container/client-game-view-container-const';
import {GameLandscapeItemEnum, GameLandscapeType} from '../../../../model/game/landscape/landscape-type';
import {GameBuildingType} from '../../../../model/game/building/building-type';
import {getLandscapeByPoint} from '../../../../model/game/landscape/landscape-helper';

import {SizeType} from '../../../../model/map/map-type';

import {landscapeImage} from './image/landscape-image';

const {br0, wt0, st0, tr0, hl0, fr1, rd0, st1, tr1, tr2, fr0, tr3, tr4, wt1, wt2, br1} = GameLandscapeItemEnum;
const landscapeCache: Record<string, string> = {};

const mainImageMap: Record<GameLandscapeItemEnum, HTMLImageElement> = {
    [br0]: landscapeImage.bridge0,
    [br1]: landscapeImage.bridge1,
    [fr0]: landscapeImage.forest0,
    [fr1]: landscapeImage.forest1,
    [hl0]: landscapeImage.hill0,
    [rd0]: landscapeImage.road0,
    [st0]: landscapeImage.stone0,
    [st1]: landscapeImage.stone1,
    [tr0]: landscapeImage.terra0,
    [tr1]: landscapeImage.terra1,
    [tr2]: landscapeImage.terra2,
    [tr3]: landscapeImage.terra3,
    [tr4]: landscapeImage.terra4,
    [wt0]: landscapeImage.water0,
    [wt1]: landscapeImage.water1,
    [wt2]: landscapeImage.water2,
};

// eslint-disable-next-line complexity, sonarjs/cognitive-complexity
function get3x3landscape(point: PointType, landscape: GameLandscapeType): Array<GameLandscapeItemEnum> {
    const {x, y} = point;
    const centerTile: GameLandscapeItemEnum = getLandscapeByPoint(point, landscape) || rd0;

    const leftUpTile: GameLandscapeItemEnum = getLandscapeByPoint({x: x - 1, y: y - 1}, landscape) || centerTile;
    const upTile: GameLandscapeItemEnum = getLandscapeByPoint({x, y: y - 1}, landscape) || centerTile;
    const rightUpTile: GameLandscapeItemEnum = getLandscapeByPoint({x: x + 1, y: y - 1}, landscape) || centerTile;

    const leftTile: GameLandscapeItemEnum = getLandscapeByPoint({x: x - 1, y}, landscape) || centerTile;
    const rightTile: GameLandscapeItemEnum = getLandscapeByPoint({x: x + 1, y}, landscape) || centerTile;

    const leftBottomTile: GameLandscapeItemEnum = getLandscapeByPoint({x: x - 1, y: y + 1}, landscape) || centerTile;
    const bottomTile: GameLandscapeItemEnum = getLandscapeByPoint({x, y: y + 1}, landscape) || centerTile;
    const rightBottomTile: GameLandscapeItemEnum = getLandscapeByPoint({x: x + 1, y: y + 1}, landscape) || centerTile;

    return [
        leftUpTile,
        upTile,
        rightUpTile,
        leftTile,
        centerTile,
        rightTile,
        leftBottomTile,
        bottomTile,
        rightBottomTile,
    ];
}

function getIsBridge(tileName: GameLandscapeItemEnum): boolean {
    return tileName === br0 || tileName === br1;
}

function getIsNotBridge(tileName: GameLandscapeItemEnum): boolean {
    return !getIsBridge(tileName);
}

function getIsEqualType(tileNameA: GameLandscapeItemEnum, tileNameB: GameLandscapeItemEnum): boolean {
    return tileNameA.replace(/\d+/gi, '').toLowerCase() === tileNameB.replace(/\d+/gi, '').toLowerCase();
}

function getIsNotEqualType(tileNameA: GameLandscapeItemEnum, tileNameB: GameLandscapeItemEnum): boolean {
    return !getIsEqualType(tileNameA, tileNameB);
}

// eslint-disable-next-line complexity
function getIsNeedSmallAngle(
    baseTile: GameLandscapeItemEnum,
    baseTileA: GameLandscapeItemEnum,
    baseTileB: GameLandscapeItemEnum,
    testTitle: GameLandscapeItemEnum
): boolean {
    return (
        (getIsEqualType(baseTile, baseTileA) || getIsBridge(baseTileA)) &&
        (getIsEqualType(baseTile, baseTileB) || getIsBridge(baseTileB)) &&
        getIsNotEqualType(baseTile, testTitle) &&
        getIsNotBridge(testTitle)
    );
}

// eslint-disable-next-line complexity
export function getIsNeedBigAngle(
    baseTile: GameLandscapeItemEnum,
    baseTileA: GameLandscapeItemEnum,
    baseTileB: GameLandscapeItemEnum
): boolean {
    return (
        getIsNotEqualType(baseTile, baseTileA) &&
        getIsNotEqualType(baseTile, baseTileB) &&
        getIsNotBridge(baseTileA) &&
        getIsNotBridge(baseTileB)
    );
}

function getIsRoadTile(tileName: GameLandscapeItemEnum): boolean {
    return tileName === rd0;
}

function getIsWaterTile(tileName: GameLandscapeItemEnum): boolean {
    return [wt0, wt1, wt2].includes(tileName);
}

// eslint-disable-next-line complexity, sonarjs/cognitive-complexity, max-statements
function drawAngles(
    canvasContext: CanvasRenderingContext2D,
    landscape3x3: Array<GameLandscapeItemEnum>,
    point: PointType
) {
    // eslint-disable-next-line prefer-destructuring
    const centerTile: GameLandscapeItemEnum = landscape3x3[4];
    const isRoadTile = getIsRoadTile(centerTile);
    const {x, y} = point;
    const angleList = isRoadTile
        ? [
              landscapeImage.angleRoad1,
              landscapeImage.angleRoad2,
              landscapeImage.angleRoad3,
              landscapeImage.angleRoad4,
              landscapeImage.angleRoad6,
              landscapeImage.angleRoad7,
              landscapeImage.angleRoad8,
              landscapeImage.angleRoad9,
              landscapeImage.angleRoad1s,
              landscapeImage.angleRoad3s,
              landscapeImage.angleRoad7s,
              landscapeImage.angleRoad9s,
          ]
        : [
              landscapeImage.angleWater1,
              landscapeImage.angleWater2,
              landscapeImage.angleWater3,
              landscapeImage.angleWater4,
              landscapeImage.angleWater6,
              landscapeImage.angleWater7,
              landscapeImage.angleWater8,
              landscapeImage.angleWater9,
              landscapeImage.angleWater1s,
              landscapeImage.angleWater3s,
              landscapeImage.angleWater7s,
              landscapeImage.angleWater9s,
          ];

    if (getIsNotBridge(landscape3x3[1]) && getIsNotEqualType(centerTile, landscape3x3[1])) {
        canvasContext.drawImage(angleList[1], x * squareSize, y * squareSize);
    }
    if (getIsNotBridge(landscape3x3[3]) && getIsNotEqualType(centerTile, landscape3x3[3])) {
        canvasContext.drawImage(angleList[3], x * squareSize, y * squareSize);
    }
    if (getIsNotBridge(landscape3x3[5]) && getIsNotEqualType(centerTile, landscape3x3[5])) {
        canvasContext.drawImage(angleList[4], x * squareSize + squareSize / 2, y * squareSize);
    }
    if (getIsNotBridge(landscape3x3[7]) && getIsNotEqualType(centerTile, landscape3x3[7])) {
        canvasContext.drawImage(angleList[6], x * squareSize, y * squareSize + squareSize / 2);
    }

    // draw small \ and / --
    // --> draw small \ and / - for left-top
    if (getIsNeedSmallAngle(centerTile, landscape3x3[1], landscape3x3[3], landscape3x3[0])) {
        canvasContext.drawImage(angleList[8], x * squareSize, y * squareSize);
    }

    // --> draw small \ and / - for right-top
    if (getIsNeedSmallAngle(centerTile, landscape3x3[1], landscape3x3[5], landscape3x3[2])) {
        canvasContext.drawImage(angleList[9], x * squareSize + squareSize / 2, y * squareSize);
    }

    // --> draw small \ and / - for left-bottom
    if (getIsNeedSmallAngle(centerTile, landscape3x3[3], landscape3x3[7], landscape3x3[6])) {
        canvasContext.drawImage(angleList[10], x * squareSize, y * squareSize + squareSize / 2);
    }

    // --> draw small \ and / - for right-bottom
    if (getIsNeedSmallAngle(centerTile, landscape3x3[5], landscape3x3[7], landscape3x3[8])) {
        canvasContext.drawImage(angleList[11], x * squareSize + squareSize / 2, y * squareSize + squareSize / 2);
    }

    // draw big \ and / --
    // --> draw big \ and / - for left-top
    if (getIsNeedBigAngle(centerTile, landscape3x3[1], landscape3x3[3])) {
        canvasContext.drawImage(angleList[0], x * squareSize, y * squareSize);
    }

    // --> draw big \ and / - for right-top
    if (getIsNeedBigAngle(centerTile, landscape3x3[1], landscape3x3[5])) {
        canvasContext.drawImage(angleList[2], x * squareSize + squareSize / 2, y * squareSize);
    }

    // --> draw big \ and / - for left-bottom
    if (getIsNeedBigAngle(centerTile, landscape3x3[3], landscape3x3[7])) {
        canvasContext.drawImage(angleList[5], x * squareSize, y * squareSize + squareSize / 2);
    }

    // --> draw big \ and / - for right-bottom
    if (getIsNeedBigAngle(centerTile, landscape3x3[5], landscape3x3[7])) {
        canvasContext.drawImage(angleList[7], x * squareSize + squareSize / 2, y * squareSize + squareSize / 2);
    }
}

function drawSquare(
    point: PointType,
    landscape: GameLandscapeType,
    landscapeItem: GameLandscapeItemEnum,
    buildingList: Array<GameBuildingType>,
    canvasContext: CanvasRenderingContext2D
) {
    const {x, y} = point;

    canvasContext.drawImage(mainImageMap[landscapeItem], x * squareSize, y * squareSize);

    // draw angles for water and road only
    if (!getIsRoadTile(landscapeItem) && !getIsWaterTile(landscapeItem)) {
        return;
    }

    const tile3x3 = get3x3landscape(point, landscape);

    drawAngles(canvasContext, tile3x3, point);
}

export function getLandscapeCanvas(
    mapSize: SizeType,
    landscape: GameLandscapeType,
    buildingList: Array<GameBuildingType>
): string {
    const landscapeKey = JSON.stringify(landscape);

    if (landscapeKey in landscapeCache) {
        return landscapeCache[landscapeKey];
    }

    const canvas = document.createElement('canvas');
    const canvasContext = canvas.getContext('2d');

    canvas.width = mapSize.width * squareSize;
    canvas.height = mapSize.height * squareSize;

    if (!canvasContext) {
        console.error('canvasContext is not defined');
        return '';
    }

    landscape.forEach((landscapeList: Array<GameLandscapeItemEnum>, y: number): void => {
        landscapeList.forEach((landscapeItem: GameLandscapeItemEnum, x: number): void => {
            drawSquare({x, y}, landscape, landscapeItem, buildingList, canvasContext);
        });
    });

    // canvasContext.drawImage(landscapeImage.water1, 50, 50, 50, 50);

    const result: string = canvas.toDataURL('image/png', 1);

    landscapeCache[landscapeKey] = result;

    return result;
}
