import {Link} from 'react-router-dom';

import {LangKeyType} from '../../provider/locale/translation/type';
import {Locale} from '../../provider/locale/locale-context';
import {classNames} from '../../util/css';

import uiButtonStyle from './ui-button.scss';

type UiLinkPropsType = {
    href: string;
    iconSrc: string;
    isDisabled?: boolean;
    langKey: LangKeyType;
};

export function UiLink(props: UiLinkPropsType): JSX.Element {
    const {href, iconSrc, langKey, isDisabled} = props;

    return (
        <Link
            className={classNames(uiButtonStyle.ui_button, {
                [uiButtonStyle.ui_button__disabled]: isDisabled,
            })}
            to={href}
        >
            <img alt={langKey} className={uiButtonStyle.ui_button__icon} src={iconSrc} />
            <span className={uiButtonStyle.ui_button__text}>
                <Locale stringKey={langKey} />
            </span>
        </Link>
    );
}
