import {PointType} from '../point/point-type';
import {GameGraveType} from '../unit/unit-type';

export function getGraveByPoint(point: PointType, graveList: Array<GameGraveType>): GameGraveType | null {
    return (
        graveList.find<GameGraveType>(
            (grave: GameGraveType): grave is GameGraveType => grave.x === point.x && grave.y === point.y
        ) || null
    );
}
