import {GamePlayerIdEnum} from '../player/player-type';

export enum GameBuildingTypeEnum {
    castle = 'castle',
    farm = 'farm',
    temple = 'temple',
    well = 'well',
}

export enum GameBuildingStateEnum {
    'buildingBroken' = 'buildingBroken',
    'buildingOk' = 'buildingOk',
}

export type GameBuildingType = {
    buildingType: GameBuildingTypeEnum;
    gamePlayerId: GamePlayerIdEnum;
    state: GameBuildingStateEnum;
    x: number;
    y: number;
};

export type BuildingInfoType = {
    canBeStore: boolean;
    canHealthAll: boolean;
    healthBonus: number;
    moneyIncome: number;
};
