import {useCallback} from 'react';
import {useNavigate} from 'react-router-dom';

import {classNames} from '../../util/css';
import {LangKeyType} from '../../provider/locale/translation/type';
import {Locale} from '../../provider/locale/locale-context';

import uiHeaderStyle from './ui-header.scss';

type UiHeaderType = {
    isFixed?: boolean;
    langKey: LangKeyType;
    onBackButtonClick?: () => void;
};

export function UiHeader(props: UiHeaderType): JSX.Element {
    const navigate = useNavigate();
    const {isFixed, langKey, onBackButtonClick} = props;
    const handleBackButtonClick = useCallback(() => {
        if (onBackButtonClick) {
            onBackButtonClick();
            return;
        }
        navigate(-1);
    }, [navigate, onBackButtonClick]);

    return (
        <header className={classNames(uiHeaderStyle.ui_header, {[uiHeaderStyle.ui_header__fixed]: isFixed})}>
            <button className={uiHeaderStyle.ui_header__back_button} onClick={handleBackButtonClick} type="button">
                &lt;&lt;
            </button>
            <span className={uiHeaderStyle.ui_header__text}>
                <Locale stringKey={langKey} />
            </span>
        </header>
    );
}
