import {ActionArgumentType, GameAnimationType} from '../game-context-type';
import {GameGraveType, GameUnitType, GameUnitTypeEnum} from '../../../model/game/unit/unit-type';
import {GameStateType} from '../../../model/game/game-type';
import {defaultUnitData} from '../../../model/game/unit/unit-const';
import {getUnitByPoint} from '../../../model/game/unit/unit-helper';
import {getGraveByPoint} from '../../../model/game/grave/grave-helper';
import {waitForNextFrame} from '../../../util/time';
import {PromiseResolveType} from '../../../util/promise';
import {getHash} from '../../../util/string';

// eslint-disable-next-line sonarjs/cognitive-complexity
export function raiseSkeletonAction(data: ActionArgumentType): Promise<void> {
    const {removeAnimationByAction, action, setAnimationList, setGameState, gameState} = data;
    const sorceress: GameUnitType | null = getUnitByPoint(action.unitCurrentPoint, gameState.unitList);
    const grave: GameGraveType | null = getGraveByPoint(action.toPoint, gameState.graveList);

    if (!sorceress || !grave) {
        return waitForNextFrame();
    }

    return new Promise<void>((resolve: PromiseResolveType<void>) => {
        setAnimationList((animList: Array<GameAnimationType>) => {
            return [
                ...animList,
                {
                    action,
                    addHealthList: [],
                    animationOnFinish: async (): Promise<void> => {
                        setGameState((currentGameState: GameStateType): GameStateType => {
                            const newUnitList = [...currentGameState.unitList];
                            const newGraveList = [...currentGameState.graveList];

                            const unitInListToUpdate: GameUnitType | null = getUnitByPoint(
                                {
                                    x: sorceress.x,
                                    y: sorceress.y,
                                },
                                newUnitList
                            );

                            if (unitInListToUpdate) {
                                newUnitList[newUnitList.indexOf(unitInListToUpdate)] = {
                                    ...unitInListToUpdate,
                                    didRaiseSkeleton: true,
                                };
                            }

                            const graveInList: GameGraveType | null = getGraveByPoint(
                                {
                                    x: grave.x,
                                    y: grave.y,
                                },
                                newGraveList
                            );

                            if (graveInList) {
                                newGraveList.splice(newGraveList.indexOf(graveInList), 1);
                            }

                            if (graveInList && unitInListToUpdate) {
                                newUnitList.push({
                                    ...defaultUnitData,
                                    gamePlayerId: unitInListToUpdate.gamePlayerId,
                                    unitId: [
                                        getHash(gameState),
                                        getHash(gameState.unitList),
                                        getHash(gameState.graveList),
                                        getHash(gameState.cemetery),
                                        gameState.stepCount,
                                    ].join('-'),
                                    unitType: GameUnitTypeEnum.skeleton,
                                    x: graveInList.x,
                                    y: graveInList.y,
                                });
                            }

                            const newGameState: GameStateType = {
                                ...currentGameState,
                                graveList: newGraveList,
                                unitList: newUnitList,
                            };

                            return newGameState;
                        });
                        removeAnimationByAction(action);
                        await waitForNextFrame();
                        resolve();
                    },
                },
            ];
        });
    });
}
