import {ActionArgumentType, GameAnimationType} from '../game-context-type';
import {GameUnitType} from '../../../model/game/unit/unit-type';
import {GameBuildingType} from '../../../model/game/building/building-type';
import {GameStateType} from '../../../model/game/game-type';
import {getBuildingByPoint} from '../../../model/game/building/building-helper';
import {getUnitByPoint} from '../../../model/game/unit/unit-helper';
import {waitForNextFrame} from '../../../util/time';
import {PromiseResolveType} from '../../../util/promise';

export function occupyBuildingAction(data: ActionArgumentType): Promise<void> {
    const {removeAnimationByAction, action, setAnimationList, setGameState, gameState} = data;
    const unitNewOwner: GameUnitType | null = getUnitByPoint(action.unitCurrentPoint, gameState.unitList);
    const buildingToOccupy: GameBuildingType | null = getBuildingByPoint(
        action.unitCurrentPoint,
        gameState.buildingList
    );

    if (!buildingToOccupy || !unitNewOwner) {
        return waitForNextFrame();
    }

    return new Promise<void>((resolve: PromiseResolveType<void>) => {
        setAnimationList((animList: Array<GameAnimationType>) => {
            return [
                ...animList,
                {
                    action,
                    addHealthList: [],
                    animationOnFinish: async (): Promise<void> => {
                        setGameState((currentGameState: GameStateType): GameStateType => {
                            const newBuildingList = [...currentGameState.buildingList];
                            const newUnitList = [...currentGameState.unitList];

                            const buildingInListToUpdate: GameBuildingType | null = getBuildingByPoint(
                                {
                                    x: buildingToOccupy.x,
                                    y: buildingToOccupy.y,
                                },
                                newBuildingList
                            );

                            const unitToUpdate: GameUnitType | null = getUnitByPoint(
                                {
                                    x: buildingToOccupy.x,
                                    y: buildingToOccupy.y,
                                },
                                newUnitList
                            );

                            if (buildingInListToUpdate) {
                                newBuildingList[newBuildingList.indexOf(buildingInListToUpdate)] = {
                                    ...buildingInListToUpdate,
                                    gamePlayerId: unitNewOwner.gamePlayerId,
                                };
                            }

                            if (unitToUpdate) {
                                newUnitList[newUnitList.indexOf(unitToUpdate)] = {
                                    ...unitToUpdate,
                                    didOccupyBuilding: true,
                                };
                            }

                            const newGameState: GameStateType = {
                                ...currentGameState,
                                buildingList: newBuildingList,
                                unitList: newUnitList,
                            };

                            return newGameState;
                        });
                        removeAnimationByAction(action);
                        await waitForNextFrame();
                        resolve();
                    },
                },
            ];
        });
    });
}
