/* global document, HTMLDivElement, KeyframeAnimationOptions, Keyframe */
import {ReactNode, useEffect, useRef, useState} from 'react';
import {createPortal} from 'react-dom';

import {animationStepTime} from '../../page/client/client-game/client-game-effect/client-game-effect-helper';
import {getRandomString} from '../../util/string';
import {useGameContext} from '../../provider/game/game-context';
import {GamePlayerColorIdEnum} from '../../model/game/player/player-type';
// eslint-disable-next-line max-len
import {colorClassNameMap} from '../../page/client/client-local-game-setup/client-local-game-setup-player/image/image-color';

import gamePopupStyle from './game-popup.scss';

type GamePopupPropsType = {
    children: ReactNode;
    color?: GamePlayerColorIdEnum | null;
    isOpen: boolean;
};

export function GamePopup(props: GamePopupPropsType): JSX.Element | null {
    const {setLocalAnimationList} = useGameContext();
    const {children, isOpen, color} = props;
    const [isViewOpen, setIsViewOpen] = useState<boolean>(false);
    const overlayNode = useRef<HTMLDivElement>(document.createElement('div'));
    const contentNode = useRef<HTMLDivElement>(document.createElement('div'));
    const popupIdRef = useRef<string>(getRandomString());

    useEffect(() => {
        const popupAnimationOptions: KeyframeAnimationOptions = {
            duration: animationStepTime * 2,
            easing: 'ease-in-out',
            fill: 'forwards',
            iterations: 1,
        };

        const overlayKeyframeList: Array<Keyframe> = [{opacity: 0}, {opacity: 1}];
        const contentKeyframeList: Array<Keyframe> = [
            {opacity: 0, transform: 'translate3d(-50%, -50%, 0) scale(2)'},
            {opacity: 1, transform: 'translate3d(-50%, -50%, 0) scale(1)'},
        ];

        if (isOpen === true && isViewOpen === false) {
            overlayNode.current.animate(overlayKeyframeList, popupAnimationOptions).onfinish = () => {
                setIsViewOpen(true);
                setLocalAnimationList((localAnimationList: Set<string>): Set<string> => {
                    return new Set<string>([...localAnimationList, popupIdRef.current]);
                });
            };

            contentNode.current.animate(contentKeyframeList, popupAnimationOptions);

            return;
        }

        if (isOpen === false && isViewOpen === true) {
            overlayNode.current.animate([...overlayKeyframeList].reverse(), popupAnimationOptions).onfinish = () => {
                setIsViewOpen(false);
                setLocalAnimationList((localAnimationList: Set<string>): Set<string> => {
                    const newLocalAnimationList: Set<string> = new Set(localAnimationList);

                    newLocalAnimationList.delete(popupIdRef.current);

                    return newLocalAnimationList;
                });
            };
            contentNode.current.animate([...contentKeyframeList].reverse(), popupAnimationOptions);
        }
    }, [popupIdRef, isOpen, isViewOpen, setLocalAnimationList]);

    if (typeof document === 'undefined') {
        return null;
    }

    if (isOpen === false && isViewOpen === false) {
        return null;
    }

    const contentClassName: string = `${gamePopupStyle.game_popup__content} ${
        color ? colorClassNameMap[color].bgGradient : ''
    }`.trim();

    return createPortal(
        <>
            <div className={gamePopupStyle.game_popup__overlay} ref={overlayNode} />
            <div className={contentClassName} ref={contentNode}>
                {children}
            </div>
        </>,
        document.body
    );
}
