import {GamePlayerIdEnum} from '../player/player-type';
import {GameBuildingTypeEnum} from '../building/building-type';
import {LangKeyType} from '../../../provider/locale/translation/type';
import {PointType} from '../point/point-type';

import {MovePathType} from './unit-path';

export enum GameUnitTypeEnum {
    archer = 'archer',
    catapult = 'catapult',
    crystal = 'crystal',
    demonLord = 'demon-lord',
    direWolf = 'dire-wolf',
    dragon = 'dragon',
    elemental = 'elemental',
    galamar = 'galamar',
    golem = 'golem',
    saeth = 'saeth',
    skeleton = 'skeleton',
    soldier = 'soldier',
    sorceress = 'sorceress',
    valadorn = 'valadorn',
    wisp = 'wisp',
}

export enum GameUnitMoveTypeEnum {
    float = 'float',
    fly = 'fly',
    walk = 'walk',
}

export type GameUnitType = {
    damageGiven: number;
    damageReceived: number;
    deathCount: number;
    didAttackBuilding: boolean;
    didAttackUnit: boolean;
    didFixBuilding: boolean;
    didMove: boolean;
    didOccupyBuilding: boolean;
    didRaiseSkeleton: boolean;
    gamePlayerId: GamePlayerIdEnum;
    health: number;
    poisonTurnCount: number;
    unitId: string;
    unitType: GameUnitTypeEnum;
    x: number;
    y: number;
};

export type GameGraveType = {
    createdStepCount: number;
    x: number;
    y: number;
};

export type UnitInfoType = {
    armor: number;
    attack: {
        max: number;
        min: number;
        range: number;
    };
    attackPoisonCount: number;
    bonusAttackVersus: Record<GameUnitMoveTypeEnum, number>;
    bonusAttackVersusSkeleton: number;
    canAttackAfterMove: boolean;
    canBePoisoned: boolean;
    canFixBuilding: boolean;
    canMeleeAttack: boolean;
    cost: number;
    destroyBuildingList: Array<GameBuildingTypeEnum>;
    hasGrave: boolean;
    isHero: boolean;
    isWispAuraAccept: boolean;
    langKey: {
        description: LangKeyType;
        name: LangKeyType;
    };
    moveSize: number;
    moveType: GameUnitMoveTypeEnum;
    occupyBuildingList: Array<GameBuildingTypeEnum>;
    raiseSkeletonRange: number;
    wispAuraRange: number;
};

export type GameAllActionsType = {
    attackUnitToBuildingPointList: Array<PointType>;
    attackUnitToUnitPointList: Array<PointType>;
    canFixBuilding: boolean;
    canOccupyBuilding: boolean;
    canOpenStore: boolean;
    moveOnAllyPointList: Array<PointType>;
    movePathList: Array<MovePathType>;
    movePointList: Array<PointType>;
    raiseSkeletonPointList: Array<PointType>;
    unitCurrentPoint: PointType;
    unitId: string;
};
