/* global HTMLSelectElement */
/* eslint-disable react/jsx-max-depth */

import {SyntheticEvent, useCallback, useState, useEffect} from 'react';
import {useNavigate, useParams} from 'react-router-dom';

import {Page} from '../../../layout/page/page';
import {appRoute} from '../../../app/app-route';
import {GamePlayerIdEnum, GamePlayerType, GamePlayerTypeEnum} from '../../../model/game/player/player-type';
import {useGameContext} from '../../../provider/game/game-context';
import {ExtractPathKeysType} from '../../../util/url';
import {GameMapType} from '../../../model/map/map-type';
import {getMapById} from '../../../model/map/map-helper';
import {GameBuildingType} from '../../../model/game/building/building-type';
import {GameUnitType} from '../../../model/game/unit/unit-type';
import {getDefaultGamePlayerList} from '../../../model/game/player/player-helper';
import {startedMoneyList, unitLimitList} from '../../../model/game/game-const';
import {UiHeader} from '../../../layout/ui-header/ui-header';
import {Locale} from '../../../provider/locale/locale-context';
import {UiButton} from '../../../layout/ui-button/ui-button';
import {uiButtonImage} from '../../../layout/ui-button/image/ui-button-image';

import clientLocalGameSetupStyle from './client-local-game-setup.scss';
import {ClientLocalGameSetupPlayer} from './client-local-game-setup-player/client-local-game-setup-player';

// eslint-disable-next-line max-statements
export function ClientLocalGameSetup(): JSX.Element {
    const {setGameState, resetState, setIsOpenStore} = useGameContext();
    const navigate = useNavigate();
    const {mapId = ''} = useParams<ExtractPathKeysType<typeof appRoute.setupMap.path>>();
    const gameMap: GameMapType | null = getMapById(mapId);
    const [playerList, setPlayerList] = useState<Array<GamePlayerType>>(
        getDefaultGamePlayerList().slice(0, gameMap?.maxPlayer)
    );
    const [startedMoney, setStartedMoney] = useState<number>(startedMoneyList[0]);
    const [unitLimit, setUnitLimit] = useState<number>(unitLimitList[0]);

    useEffect(resetState, [resetState]);

    const handleStartGame = useCallback(() => {
        if (!gameMap) {
            console.error(`can not find map by id: ${mapId}`);
            return;
        }

        const playerIdList: Set<GamePlayerIdEnum> = new Set(
            playerList
                .filter<GamePlayerType>((playerToFilter: GamePlayerType): playerToFilter is GamePlayerType => {
                    return playerToFilter.playerType !== GamePlayerTypeEnum.none;
                })
                .map<GamePlayerIdEnum>((player: GamePlayerType): GamePlayerIdEnum => player.gamePlayerId)
        );

        setIsOpenStore(false);

        setGameState({
            activeGamePlayerId: playerList[0].gamePlayerId,
            buildingList: gameMap.buildingList.map<GameBuildingType>((building: GameBuildingType): GameBuildingType => {
                if (playerIdList.has(building.gamePlayerId)) {
                    return building;
                }
                return {
                    ...building,
                    gamePlayerId: GamePlayerIdEnum.playerEmpty,
                };
            }),
            cemetery: gameMap.cemetery,
            // TODO: check only local player
            clientGamePlayerIdList: playerList.map<GamePlayerIdEnum>(
                (player: GamePlayerType): GamePlayerIdEnum => player.gamePlayerId
            ),
            doneTriggerIsList: [],
            gameSettings: {
                startedMoney,
                unitLimit,
            },
            graveList: gameMap.graveList,
            landscape: gameMap.landscape,
            mapId: gameMap.mapId,
            playerList: playerList.map<GamePlayerType>((playerInList: GamePlayerType): GamePlayerType => {
                return {
                    ...playerInList,
                    money: startedMoney,
                };
            }),
            stepCount: 0,
            unitList: gameMap.unitList.filter<GameUnitType>((unit: GameUnitType): unit is GameUnitType =>
                playerIdList.has(unit.gamePlayerId)
            ),
        });
        navigate(appRoute.clientGame.path);
    }, [setIsOpenStore, mapId, playerList, setGameState, navigate, gameMap, startedMoney, unitLimit]);

    if (!gameMap) {
        return <Page>NO MAP</Page>;
    }

    return (
        <Page>
            <div className={clientLocalGameSetupStyle.client_local_game_setup}>
                <UiHeader langKey="UI__BUTTON__SKIRMISH" />

                <div className={clientLocalGameSetupStyle.client_local_game_setup__content}>
                    <div className={clientLocalGameSetupStyle.client_local_game_setup__border}>
                        <h3 className={clientLocalGameSetupStyle.client_local_game_setup__map_name}>{gameMap.name}</h3>

                        {playerList
                            .slice(0, gameMap.maxPlayer)
                            .map<JSX.Element>((playerInList: GamePlayerType): JSX.Element => {
                                return (
                                    <ClientLocalGameSetupPlayer
                                        key={playerInList.gamePlayerId}
                                        player={playerInList}
                                        setPlayerList={setPlayerList}
                                    />
                                );
                            })}
                        <div className={clientLocalGameSetupStyle.client_local_game_setup__item}>
                            <p className={clientLocalGameSetupStyle.client_local_game_setup__item_label}>
                                <Locale stringKey="SETUP_SKIRMISH__MONEY" />
                            </p>
                            <div className={clientLocalGameSetupStyle.client_local_game_setup__item_input_wrapper}>
                                {/* eslint-disable-next-line jsx-a11y/no-onchange */}
                                <select
                                    className={clientLocalGameSetupStyle.client_local_game_setup__item_select}
                                    defaultValue={startedMoney}
                                    onChange={(evt: SyntheticEvent<HTMLSelectElement>) => {
                                        console.log(evt.currentTarget.value);
                                        setStartedMoney(Number.parseInt(evt.currentTarget.value, 10));
                                    }}
                                >
                                    {startedMoneyList.map<JSX.Element>((money: number): JSX.Element => {
                                        return (
                                            <option key={money} value={money}>
                                                {money}
                                            </option>
                                        );
                                    })}
                                </select>
                            </div>
                        </div>

                        <div className={clientLocalGameSetupStyle.client_local_game_setup__item}>
                            <p className={clientLocalGameSetupStyle.client_local_game_setup__item_label}>
                                <Locale stringKey="SETUP_SKIRMISH__UNIT_LIMIT" />
                            </p>

                            <div className={clientLocalGameSetupStyle.client_local_game_setup__item_input_wrapper}>
                                {/* eslint-disable-next-line jsx-a11y/no-onchange */}
                                <select
                                    className={clientLocalGameSetupStyle.client_local_game_setup__item_select}
                                    defaultValue={unitLimit}
                                    onChange={(evt: SyntheticEvent<HTMLSelectElement>) => {
                                        console.log(evt.currentTarget.value);
                                        setUnitLimit(Number.parseInt(evt.currentTarget.value, 10));
                                    }}
                                >
                                    {unitLimitList.map<JSX.Element>((unitLimitValue: number): JSX.Element => {
                                        return (
                                            <option key={unitLimitValue} value={unitLimitValue}>
                                                {unitLimitValue}
                                            </option>
                                        );
                                    })}
                                </select>
                            </div>
                        </div>
                    </div>
                </div>

                <div className={clientLocalGameSetupStyle.client_local_game_setup__footer}>
                    <UiButton
                        iconSrc={uiButtonImage.sword.src}
                        langKey="SETUP_SKIRMISH__FIGHT"
                        onClick={handleStartGame}
                    />
                </div>
            </div>
        </Page>
    );
}
