/* global localStorage */
import {GameStateType} from '../../../model/game/game-type';
import {getRandomString} from '../../../util/string';

const savedGameListKey = 'saved-game-list-v.1.0';

export type SavedGameType = {
    date: string;
    gameState: GameStateType;
    id: string;
    name: string;
};

export function getSavedGameList(): Array<SavedGameType> {
    const savedGameListRaw: string = localStorage.getItem(savedGameListKey) || '[]';
    const savedGameList: Array<SavedGameType> = JSON.parse(savedGameListRaw);

    return savedGameList;
}

export function saveGame(name: string, gameState: GameStateType) {
    const gameToSave: SavedGameType = {
        date: new Date().toISOString(),
        gameState,
        id: getRandomString(),
        name,
    };

    const savedGameList: Array<SavedGameType> = [...getSavedGameList(), gameToSave];

    localStorage.setItem(savedGameListKey, JSON.stringify(savedGameList));
}

export function deleteSavedGame(idToDelete: string) {
    const savedGameList: Array<SavedGameType> = getSavedGameList().filter<SavedGameType>(
        (savedGame: SavedGameType): savedGame is SavedGameType => savedGame.id !== idToDelete
    );

    localStorage.setItem(savedGameListKey, JSON.stringify(savedGameList));
}

export function renameSavedGame(data: Record<'idToRename' | 'newName', string>) {
    const {idToRename, newName} = data;
    const savedGameList: Array<SavedGameType> = getSavedGameList();
    const foundSavedGame: SavedGameType | void = savedGameList.find<SavedGameType>(
        (savedGame: SavedGameType): savedGame is SavedGameType => savedGame.id === idToRename
    );

    if (foundSavedGame) {
        foundSavedGame.name = newName;
    }

    localStorage.setItem(savedGameListKey, JSON.stringify(savedGameList));
}
