import {useGameContext} from '../../../../provider/game/game-context';
import {GameBuildingType} from '../../../../model/game/building/building-type';

import {ClientGameBuildingEffectItem} from './client-game-building-effect-item';
import clientGameBuildingEffectStyle from './client-game-building-effect.scss';

export function ClientGameBuildingEffect(): JSX.Element {
    const {gameState} = useGameContext();
    const {buildingList} = gameState;

    return (
        <div className={clientGameBuildingEffectStyle.building_effect_layer}>
            {buildingList.map((building: GameBuildingType): JSX.Element => {
                return <ClientGameBuildingEffectItem building={building} key={`${building.x}-${building.y}`} />;
            })}
        </div>
    );
}
