/* global document, HTMLDivElement, HTMLImageElement, Keyframe, KeyframeAnimationOptions */

import {useEffect, useRef, useState} from 'react';

import {useGameContext} from '../../../../../provider/game/game-context';
import {squareSize} from '../../client-game-view-container/client-game-view-container-const';
import {GameUnitType} from '../../../../../model/game/unit/unit-type';
import {PointType} from '../../../../../model/game/point/point-type';
import {GameActionTypeEnum} from '../../../../../provider/game/game-context-type';
import {getIsEqualPoint} from '../../../../../model/game/point/point-helper';
import {unitImage} from '../image/unit-image';
import {GamePlayerColorIdEnum} from '../../../../../model/game/player/player-type';
import {SmallText} from '../../game-font/text/small-text';
import {IsRender} from '../../../../../layout/is-render/is-render';
import {unitStateImage} from '../image/unit-state-image';
import {getUnitLevelData} from '../../../../../model/game/unit/unit-level';
import {BigText} from '../../game-font/text/big-text';
import {IsHidden} from '../../../../../layout/is-hidden/is-hidden';
import {unitOtherImage} from '../image/unit-other-image';
import {animationMoveStepTime, animationStepTime} from '../../client-game-effect/client-game-effect-helper';
import {getHasUnitAvailableAction, getUnitAvailableActionsData} from '../../../../../model/game/unit/unit-action';
import {getIsUnitPoisoned, getIsUnitUnderWispAura} from '../../../../../model/game/unit/unit-state';
import {getUnitToUnitAttackResult} from '../../../../../model/game/unit/unit-to-unit-attack';
import {getUnitByPoint, getUnitByUnitId} from '../../../../../model/game/unit/unit-helper';
import {getRandomString} from '../../../../../util/string';

import {
    getImageListByUnit,
    levelUpKeyList,
    unitAddHealthList,
    unitAnimationOption,
} from './client-game-unit-item-helper';
import clientGameUnitItemStyle from './client-game-unit-item.scss';

type ClientGameUnitItemPropsType = {
    unit: GameUnitType;
};

// eslint-disable-next-line max-statements
export function ClientGameUnitItem(props: ClientGameUnitItemPropsType): JSX.Element {
    const {unit: unitFromState} = props;
    const {gameState, animationList, setAvailableActions, mapSize, setLocalAnimationList, setActivePoint} =
        useGameContext();
    const [isNeedToShowActions, setIsNeedToShowActions] = useState<boolean>(false);
    const [isLevelUpShowing, setIsLevelUpShowing] = useState<boolean>(false);
    const [changedHealth, setChangedHealth] = useState<number>(0);
    const {playerList, unitList, stepCount} = gameState;
    const wrapper = useRef<HTMLDivElement>(document.createElement('div'));
    const healthChange = useRef<HTMLDivElement>(document.createElement('div'));
    const levelUp = useRef<HTMLImageElement>(document.createElement('img'));
    const first = animationList.at(0);
    const [unitToView, setUnitToView] = useState<GameUnitType>({...unitFromState});
    const unitPrevRef = useRef<GameUnitType>({...unitToView});
    const {y, x, unitType, health, damageGiven, unitId} = unitToView;

    const imageList: Readonly<[HTMLImageElement, HTMLImageElement]> = getHasUnitAvailableAction(unitToView, gameState)
        ? getImageListByUnit(unitToView, playerList)
        : unitImage[unitType][GamePlayerColorIdEnum.gray];

    useEffect(() => {
        console.log('unitFromState', unitFromState.x, unitFromState.y);
        setUnitToView({...unitFromState});
    }, [unitFromState]);

    useEffect(() => {
        const {health: currentUnitHealth, damageGiven: currentUnitDamageGiven} = unitToView;

        const isShowLevelUp =
            getUnitLevelData(currentUnitDamageGiven).level !== getUnitLevelData(unitPrevRef.current.damageGiven).level;

        if (isShowLevelUp) {
            const levelUpTiming: KeyframeAnimationOptions = {
                duration: animationStepTime * (levelUpKeyList.length - 1),
                ...unitAnimationOption,
            };

            const animation = levelUp.current.animate(levelUpKeyList, levelUpTiming);

            setIsLevelUpShowing(true);
            const levelUpAnimationId = getRandomString();

            setLocalAnimationList((idList: Set<string>): Set<string> => {
                return new Set<string>([...idList, levelUpAnimationId]);
            });

            animation.onfinish = () => {
                setIsLevelUpShowing(false);
                setLocalAnimationList((idList: Set<string>): Set<string> => {
                    const newIdList: Set<string> = new Set<string>(idList);

                    newIdList.delete(levelUpAnimationId);

                    return newIdList;
                });
            };

            unitPrevRef.current = {
                ...unitToView,
            };
        }

        if (currentUnitHealth !== unitPrevRef.current.health) {
            const healthChangeTiming: KeyframeAnimationOptions = {
                ...unitAnimationOption,
                duration: animationStepTime * (unitAddHealthList.length - 1),
            };

            const animation = healthChange.current.animate(unitAddHealthList, healthChangeTiming);

            setChangedHealth(currentUnitHealth - unitPrevRef.current.health);

            animation.onfinish = () => {
                setChangedHealth(0);
            };

            unitPrevRef.current = {
                ...unitToView,
            };
        }
    }, [unitToView, setLocalAnimationList]);

    useEffect(() => {
        if (isNeedToShowActions === false) {
            return;
        }

        setIsNeedToShowActions(false);

        const unitFromGameState: GameUnitType | null = getUnitByUnitId(unitToView.unitId, unitList);

        if (unitFromGameState) {
            setActivePoint({x: unitFromGameState.x, y: unitFromGameState.y});
            setAvailableActions(getUnitAvailableActionsData(unitFromGameState, gameState));
        }
    }, [setActivePoint, unitToView, isNeedToShowActions, setAvailableActions, gameState, unitList]);

    // animate health and level change
    // eslint-disable-next-line complexity, max-statements
    useEffect(() => {
        if (!first) {
            return;
        }
        const {action} = first;

        if (action.actionType !== GameActionTypeEnum.unitToUnitAttack) {
            return;
        }

        const {unitList: unitListInEffect} = gameState;
        const aggressorBefore: GameUnitType | null = getUnitByPoint(action.unitCurrentPoint, unitListInEffect);
        const defenderBefore: GameUnitType | null = getUnitByPoint(action.toPoint, unitListInEffect);

        if (!aggressorBefore || !defenderBefore) {
            console.error('can not find units');
            console.error(aggressorBefore, defenderBefore);
            return;
        }

        const isMeAggressor = getIsEqualPoint({x, y}, action.unitCurrentPoint);
        const isMeDefender = getIsEqualPoint({x, y}, action.toPoint);

        if (!isMeAggressor && !isMeDefender) {
            return;
        }

        const {aggressor, defender, hasStrikeBack} = getUnitToUnitAttackResult(
            {aggressor: aggressorBefore, defender: defenderBefore},
            gameState
        );

        if (isMeDefender) {
            const defenderAnimationId = getRandomString();

            setLocalAnimationList((idList: Set<string>): Set<string> => {
                return new Set<string>([...idList, defenderAnimationId]);
            });
            const emptyNode: HTMLDivElement = document.createElement('div');
            const emptyAnimation = emptyNode.animate([], {
                duration: animationStepTime * 2,
            });

            emptyAnimation.onfinish = () => {
                setUnitToView(current => {
                    return {...current, health: defender.health};
                });

                setLocalAnimationList((idList: Set<string>): Set<string> => {
                    const newIdList: Set<string> = new Set<string>(idList);

                    newIdList.delete(defenderAnimationId);

                    return newIdList;
                });
            };
        }

        if (isMeAggressor && hasStrikeBack) {
            const aggressorAnimationId = getRandomString();

            setLocalAnimationList((idList: Set<string>): Set<string> => {
                return new Set<string>([...idList, aggressorAnimationId]);
            });

            const emptyNode: HTMLDivElement = document.createElement('div');
            const emptyAnimation = emptyNode.animate([], {
                duration: animationStepTime * 5,
            });

            emptyAnimation.onfinish = () => {
                setUnitToView(current => {
                    return {...current, health: aggressor.health};
                });

                setLocalAnimationList((idList: Set<string>): Set<string> => {
                    const newIdList: Set<string> = new Set<string>(idList);

                    newIdList.delete(aggressorAnimationId);

                    return newIdList;
                });
            };
        }
    }, [first, x, y, gameState, unitToView, setLocalAnimationList]);

    // eslint-disable-next-line complexity
    useEffect(() => {
        if (!first) {
            return;
        }
        const {action, animationOnFinish} = first;

        const isMoveAction: boolean =
            action.actionType === GameActionTypeEnum.unitMove ||
            action.actionType === GameActionTypeEnum.unitMoveRevert;

        if (!isMoveAction || action.unitId !== unitId) {
            return;
        }

        if (!getIsEqualPoint(action.unitCurrentPoint, {x, y})) {
            return;
        }

        const moveTransformList: Array<Keyframe> = action.movePointList.map<Keyframe>((point: PointType): Keyframe => {
            return {transform: `translate3d(${point.x * squareSize}px, ${point.y * squareSize}px, 0)`};
        });

        const unitMoveTiming: KeyframeAnimationOptions = {
            duration: animationMoveStepTime * moveTransformList.length,
            ...unitAnimationOption,
        };

        const animation = wrapper.current.animate(moveTransformList, unitMoveTiming);

        animation.onfinish = () => {
            animationOnFinish();
            setIsNeedToShowActions(true);
        };
    }, [first, x, y, unitId]);

    const unitLevel = getUnitLevelData(damageGiven);

    return (
        <>
            <IsHidden isHidden={changedHealth === 0}>
                <div
                    className={clientGameUnitItemStyle.unit_change_health}
                    ref={healthChange}
                    style={{
                        left: x * squareSize,
                        top: y * squareSize,
                        zIndex: y + mapSize.height,
                    }}
                >
                    <BigText text={Math.round(changedHealth).toString(10)} />
                </div>
            </IsHidden>
            <IsHidden isHidden={!isLevelUpShowing}>
                <img
                    alt="level up"
                    className={clientGameUnitItemStyle.unit_level_up}
                    ref={levelUp}
                    src={unitOtherImage.levelUp.src}
                    style={{
                        left: x * squareSize,
                        top: y * squareSize,
                        zIndex: y + mapSize.height,
                    }}
                />
            </IsHidden>
            <div
                className={clientGameUnitItemStyle.unit_square}
                data-unit={JSON.stringify(unitToView, null, 4)}
                ref={wrapper}
                style={{
                    transform: `translate3d(${x * squareSize}px, ${y * squareSize}px, 0)`,
                    zIndex: y,
                }}
            >
                <div className={clientGameUnitItemStyle.unit_image_wrapper}>
                    <img alt={unitType} className={clientGameUnitItemStyle.unit_image} src={imageList[0].src} />
                    <img alt={unitType} className={clientGameUnitItemStyle.unit_image} src={imageList[1].src} />
                </div>
                <div className={clientGameUnitItemStyle.unit_additional_state_wrapper}>
                    <IsRender isRender={getIsUnitUnderWispAura(unitToView, playerList, unitList, mapSize)}>
                        <img
                            alt="wisp aura"
                            className={clientGameUnitItemStyle.unit_additional_state_image}
                            src={unitStateImage.wispAura.src}
                        />
                    </IsRender>
                    <IsRender isRender={getIsUnitPoisoned(unitToView, stepCount)}>
                        <img
                            alt="under poison"
                            className={clientGameUnitItemStyle.unit_additional_state_image}
                            src={unitStateImage.poison.src}
                        />
                    </IsRender>
                </div>
                <IsRender isRender={Math.ceil(health) !== 100}>
                    <SmallText className={clientGameUnitItemStyle.unit_health} text={Math.ceil(health).toString(10)} />
                </IsRender>
                <IsRender isRender={unitLevel.level > 0}>
                    <SmallText
                        className={clientGameUnitItemStyle.unit_level}
                        text={Math.round(unitLevel.level).toString(10)}
                    />
                </IsRender>
            </div>
        </>
    );
}
