import {useCallback, useEffect} from 'react';

import {useGameContext} from '../../../../provider/game/game-context';
import {PointType} from '../../../../model/game/point/point-type';
import {defaultAvailableActions} from '../../../../provider/game/game-context-const';
import {squareSize} from '../client-game-view-container/client-game-view-container-const';
import {IsRender} from '../../../../layout/is-render/is-render';
import {GameAllActionsType} from '../../../../model/game/unit/unit-type';
import {getLandscapePointList} from '../../../../model/game/landscape/landscape-helper';

import clientGameClickHunterStyle from './client-game-click-hunter.scss';
import {ClientGameClickHunterCell} from './client-game-click-hunter-cell/client-game-click-hunter-cell';
import {clientGameClickHunterImage} from './image/client-game-click-hunter-image';

export function ClientGameClickHunter(): JSX.Element {
    const {getAvailableActions, gameState, animationList, setAvailableActions, activePoint, localAnimationList} =
        useGameContext();
    const {activeGamePlayerId, clientGamePlayerIdList, landscape} = gameState;

    const handleShowAvailableActions = useCallback(
        (point: PointType) => {
            const gameAllActions: GameAllActionsType = getAvailableActions({
                activeGamePlayerId,
                clientGamePlayerIdList,
                point,
            });

            setAvailableActions(gameAllActions);
        },
        [clientGamePlayerIdList, getAvailableActions, activeGamePlayerId, setAvailableActions]
    );

    useEffect(() => {
        if (animationList.length > 0) {
            setAvailableActions(defaultAvailableActions);
        }
    }, [animationList, setAvailableActions]);

    if (animationList.length > 0 || localAnimationList.size > 0) {
        return <div className={clientGameClickHunterStyle.click_hunter_layer} />;
    }

    return (
        <div className={clientGameClickHunterStyle.click_hunter_layer}>
            {getLandscapePointList(landscape).map<JSX.Element>((landscapePoint: PointType): JSX.Element => {
                return (
                    <ClientGameClickHunterCell
                        handleShowAvailableActions={handleShowAvailableActions}
                        key={`${landscapePoint.x.toString(16)}-${landscapePoint.y.toString(16)}`}
                        x={landscapePoint.x}
                        y={landscapePoint.y}
                    />
                );
            })}
            <IsRender isRender={activePoint.x >= 0 && activePoint.y >= 0}>
                <div
                    className={clientGameClickHunterStyle.click_hunter_active_point}
                    style={{transform: `translate(${activePoint.x * squareSize}px, ${activePoint.y * squareSize}px)`}}
                >
                    <div className={clientGameClickHunterStyle.click_hunter_active_point_animation_wrapper}>
                        <img
                            alt="select-mark-0"
                            className={clientGameClickHunterStyle.click_hunter_active_point_animation_image}
                            src={clientGameClickHunterImage.selectMark0.src}
                        />
                        <img
                            alt="select-mark-1"
                            className={clientGameClickHunterStyle.click_hunter_active_point_animation_image}
                            src={clientGameClickHunterImage.selectMark1.src}
                        />
                    </div>
                </div>
            </IsRender>
        </div>
    );
}
