import {GameStateType} from '../../model/game/game-type';
import {
    GamePlayerColorIdEnum,
    GamePlayerIdEnum,
    GamePlayerTeamIdEnum,
    GamePlayerType,
    GamePlayerTypeEnum,
} from '../../model/game/player/player-type';
import {startedMoneyList, unitLimitList} from '../../model/game/game-const';

export function getDefaultGameState(): GameStateType {
    return {
        activeGamePlayerId: GamePlayerIdEnum.player1,
        buildingList: [],
        cemetery: [],
        clientGamePlayerIdList: [GamePlayerIdEnum.player1, GamePlayerIdEnum.player2],
        doneTriggerIsList: [],
        gameSettings: {
            startedMoney: startedMoneyList[0],
            unitLimit: unitLimitList[0],
        },
        graveList: [],
        // isOpenStore: false,
        landscape: [],
        mapId: '',
        playerList: [
            {
                gamePlayerColorId: GamePlayerColorIdEnum.blue,
                gamePlayerId: GamePlayerIdEnum.player1,
                gamePlayerTeamId: GamePlayerTeamIdEnum.team1,
                money: 10_000,
                playerType: GamePlayerTypeEnum.human,
                // playerId: 'just-id-one',
            },
            {
                gamePlayerColorId: GamePlayerColorIdEnum.red,
                gamePlayerId: GamePlayerIdEnum.player2,
                gamePlayerTeamId: GamePlayerTeamIdEnum.team2,
                money: 10_000,
                playerType: GamePlayerTypeEnum.human,
                // playerId: 'just-id-two',
            },
        ],
        stepCount: 0,
        unitList: [],
    };
}

export function getNextPlayerId(
    activeGamePlayerId: GamePlayerIdEnum,
    playerList: Array<GamePlayerType>
): GamePlayerIdEnum {
    const currentPlayerIndex = playerList.findIndex((player: GamePlayerType): boolean => {
        return player.gamePlayerId === activeGamePlayerId;
    });

    if (currentPlayerIndex === -1) {
        throw new Error('currentPlayerIndex === -1');
    }

    if (currentPlayerIndex === playerList.length - 1) {
        return playerList[0].gamePlayerId;
    }

    return playerList[currentPlayerIndex + 1].gamePlayerId;
}
